/* eslint-disable no-continue */
/* eslint-disable guard-for-in */
/* eslint-disable no-restricted-syntax */
/* eslint-disable no-useless-escape */
/* eslint-disable complexity */
import React from "react";
import {
    Search, Button, Icon, Segment, SegmentGroup, Radio, Form, Select, Header, Input, Feed,
} from "semantic-ui-react";
import classnames from "classnames";
import { Chart } from "react-google-charts";
import MultiSlider from "multi-slider";
import {
    shape, func, arrayOf, bool,
} from "prop-types";
import countryList from "iso-3166-country-list";

const countries = countryList.map( country => ( { key: country.code, text: country.name, value: country.code } ) );
const palette = [ "#eb4034", "#ffbb00", "#a6ff00", "#00ffd0", "#009dff", "#002cf2", "#6a00ff", "#ea00ff" ];
const defaultState = {
    splitType: "equal",
    splits: new Map(),
    authors: [ {
        name: null,
        id: null,
    } ],
    publishers: [ {
        name: null,
        id: null,
    } ],
    roles: [],
    title: null,
    iswc: null,
    territories: countries.map( c => c.value ),
    createdIn: [],
    unverifiedDerivativeISRCs: [],
    unverifiedOriginatingISWCs: [],
    selectCountries: false,
    ownerSearchQuery: null,
};
class RegisterWorkForm extends React.Component {
    constructor( props ) {
        super( props );

        this.state = defaultState;
    }

    handleChangeSplitType = ( e, { value } ) => {
        const { authors, publishers } = this.state;
        const owners = [ ...authors, ...publishers ];
        if ( value === "equal" ) {
            const newSplits = new Map();
            owners.forEach( ( owner ) => {
                if ( owner.name && owner.id ) {
                    newSplits.set( owner.id, 100 / owners.length );
                }
            } );
            this.setState( { splits: newSplits } );
        }
        this.setState( { splitType: value } );
    }

    register = () => {
        const { onRegister } = this.props;
        const {
            title, authors, roles, splits, iswc, territories, createdIn, publishers,
        } = this.state;

        // roles convert index to id or email
        const resolvedRoles = new Map();
        for ( let i = 0; i < roles.length; i += 1 ) {
            const value = roles[ i ];
            if ( !value ) {
                continue;
            }
            const idOrEmail = authors[ i ].id;
            resolvedRoles.set( idOrEmail, value );
        }
        onRegister( {
            iswc,
            createdIn,
            territories,
            title,
            publishers: publishers.filter( p => p.id != null ).map( p => p.id ),
            authors: authors.filter( a => a.id != null ).map( a => a.id ),
            roles: resolvedRoles,
            splits,
        } );
    }

    addAuthor = () => {
        const { authors } = this.state;

        this.setState( {
            authors:
            [ ...authors, { name: null, id: null } ],
        } );
    }

    addPublisher = () => {
        const { publishers } = this.state;

        this.setState( {
            publishers:
            [ ...publishers, { name: null, id: null } ],
        } );
    }

    removePublisher = ( index ) => {
        const { publishers, splits } = this.state;
        const publisher = publishers[ index ];
        splits.set( publisher.id, 0 );

        this.setState( {
            publishers: publishers.filter( ( a, i ) => i !== index ),
            splits,
        } );
    }

    removeAuthor = ( index ) => {
        const { authors, splits } = this.state;

        const author = authors[ index ];
        splits.set( author.id, 0 );

        this.setState( {
            authors: authors.filter( ( a, i ) => i !== index ),
            splits,
        } );
    }

    handleSearchAccount=( e, data, type ) => {
        const query = data.value;
        const { searchAccounts } = this.props;
        searchAccounts( { type, query } );
        this.setState( { ownerSearchQuery: query } );
    }

    setOwner=( i, data, type ) => {
        const {
            publishers, authors, splits, splitType,
        } = this.state;

        const newSplits = new Map();

        if ( splitType === "equal" ) {
            const validOwners = [ ...publishers, ...authors ].filter( a => a.id !== null );
            validOwners.forEach( ( owner ) => {
                newSplits.set( owner.id, 100 / ( validOwners.length + 1 ) );
            } );
            newSplits.set( data.id, 100 / ( validOwners.length + 1 ) );
        } else {
            splits.set( data.id, 0 );
        }

        if ( type === "author" ) {
            this.setState( {
                authors: authors.map( ( a, index ) => {
                    if ( i === index ) {
                        return data;
                    }
                    return a;
                } ),
                splits: splitType === "equal" ? newSplits : splits,
            } );
        } else if ( type === "publisher" ) {
            this.setState( {
                publishers: publishers.map( ( a, index ) => {
                    if ( i === index ) {
                        return data;
                    }
                    return a;
                } ),
                splits: splitType === "equal" ? newSplits : splits,
            } );
        }
    }

    setRole=( index, role ) => {
        const { roles } = this.state;
        roles[ index ] = role;
        this.setState( { roles } );
    }

    renderOwnerSearchResult=( { title, value } ) => {
        const invite = title.indexOf( "Invite:" ) !== -1;
        return (
            <Feed>
                <Feed.Event>
                    <Feed.Label>

                        {invite
                            ? <Icon name="mail outline" />
                            : <img alt="owner" src={ value.author.image || value.publisher.image || "https://lh3.googleusercontent.com/proxy/2ec2fsuomg4sFdIIEdMqaK0yB1c_-D-YFhGBrxd1Vzg9EbdKkdgw_34aE4-TOIBFk5283SbZLnK6hCUGkY1dpdoR8XV4DmaIz6cSF76FgF2cpe2gxdC7HDHJzVKCm_P6rZHbfUsRCQ" } />}
                    </Feed.Label>
                    <Feed.Content>
                        <Feed.Summary>
                            {title}

                        </Feed.Summary>
                    </Feed.Content>
                </Feed.Event>
            </Feed>
        );
    }

    isFormValid = () => {
        const {
            createdIn, territories, title, splits, roles, authors, publishers,
        } = this.state;
        const { account } = this.props;

        if ( !createdIn || !title || !territories || !splits || !roles || !authors ) {
            return false;
        }
        if ( createdIn.length <= 0 || territories.length <= 0 ) {
            return false;
        }
        for ( let i = 0; i < authors.length; i += 1 ) {
            if ( !roles[ i ] ) {
                return false;
            }
            if ( !splits.get( authors[ i ].id ) ) {
                return false;
            }
        }
        for ( let j = 0; j < publishers.length; j += 1 ) {
            if ( !splits.get( publishers[ j ].id ) ) {
                return false;
            }
        }

        if ( !authors.find( a => a.id === account.id ) && !publishers.find( p => p.id === account.id ) ) {
            return false;
        }
        return true;
    }

    render() {
        const {
            ownerSearchQuery, authors, splits, splitType, publishers, territories, unverifiedDerivativeISRCs, unverifiedOriginatingISWCs, selectCountries,
        } = this.state;
        const {
            account, registerWorkProcessing, authors: authorSearchResults, publishers: publisherSearchResults,
        } = this.props;
        const slices = {
        };

        const owners = [ ...authors, ...publishers ];
        owners.forEach( ( a, i ) => {
            slices[ i ] = { color: palette[ i ] };
        } );

        const pieSplits = owners.filter( owner => owner.id !== null ).map( owner => [ owner.firstname ? `${ owner.firstname } ${ owner.lastname }` : owner.id, splits.get( owner.id ) ] );
        pieSplits.unshift(
            [ "Partner", "Split percentage" ],
        );
        return (

            <div>
                <SegmentGroup>
                    <Segment>
                        <Header as="h4">Main information</Header>
                    </Segment>
                    <Segment className="form-body">
                        <Form>
                            <Form.Group widths="equal">
                                <Form.Input
                                    placeholder="Title of the work"
                                    required
                                    label="Title"
                                    onChange={ e => this.setState( { title: e.target.value } ) }
                                />
                                <Form.Input
                                    label="ISWC"
                                    placeholder="The ISWC of the work"
                                    onChange={ e => this.setState( { iswc: e.target.value } ) }
                                />
                                <Form.Dropdown
                                    label="Derivative ISRCs"
                                    placeholder="Derivative ISRCs"
                                    fluid
                                    search
                                    selection
                                    multiple
                                    allowAdditions
                                    onAddItem={ ( event, data ) => {
                                        const isrc = data.value;
                                        if ( "[A-Z]{2}-[A-Z\d]{3}-\d{2}-\d{5}".match( isrc ) ) {
                                            this.setState( { unverifiedDerivativeISRCs: [ ...unverifiedDerivativeISRCs, { key: isrc, text: isrc, value: isrc } ] } );
                                        }
                                    } }
                                    options={ unverifiedDerivativeISRCs }
                                />
                                <Form.Dropdown
                                    label="Originating ISWCs"
                                    placeholder="Originating ISWCs"
                                    fluid
                                    search
                                    selection
                                    multiple
                                    allowAdditions
                                    onAddItem={ ( event, data ) => {
                                        const iswc = data.value;
                                        if ( "[\\p{Alnum}\\-\\s]{10,13}".match( iswc ) ) {
                                            this.setState( { unverifiedOriginatingISWCs: [ ...unverifiedOriginatingISWCs, { key: iswc, text: iswc, value: iswc } ] } );
                                        }
                                    } }
                                    options={ unverifiedOriginatingISWCs }
                                />

                            </Form.Group>

                        </Form>
                    </Segment>
                </SegmentGroup>
                <SegmentGroup>
                    <Segment>
                        <Header as="h4">Authors</Header>
                    </Segment>
                    <Segment className="form-body">
                        <Form>
                            {authors.map( ( author, i ) => (
                                <Form.Group key={ author.id } widths="equal">
                                    <Form.Input
                                        required
                                        label="Name or Email"
                                    >
                                        <Search
                                            style={ { width: "100%" } }
                                            placeholder="Name or email"
                                            value={ author.firstname ? `${ author.firstname } ${ author.lastname }` : author.id }
                                            onResultSelect={ ( e, data ) => this.setOwner( i, data.result.value, "author" ) }
                                            onSearchChange={ ( e, data ) => this.handleSearchAccount( e, data, "authors" ) }
                                            resultRenderer={ this.renderOwnerSearchResult }
                                            selectFirstResult
                                            results={ authorSearchResults.length > 0
                                                ? authorSearchResults.map( a => ( { title: `${ a.firstname } ${ a.lastname }`, value: a } ) )
                                                : [ { title: `Invite: ${ ownerSearchQuery }`, value: { id: ownerSearchQuery } } ] }
                                        />
                                    </Form.Input>
                                    <Form.Input
                                        label="ISNI"
                                        placeholder="ISNI"
                                        value={ author.isni }
                                    />
                                    <Form.Input
                                        label="IPI"
                                        placeholder="IPI"
                                        value={ author.ipi }
                                    />
                                    <Form.Input
                                        label="CAE"
                                        placeholder="CAE"
                                        value={ author.cae }
                                    />
                                    <Form.Input
                                        label="Role"
                                        required
                                    >
                                        <Select
                                            onChange={ ( e, { value } ) => this.setRole( i, value ) }
                                            fluid
                                            options={ [
                                                { key: "songwriter", text: "Songwriter", value: [ "SONGWRITER" ] },
                                                { key: "composer", text: "Composer", value: [ "COMPOSER" ] },
                                                { key: "both", text: "Songwriter & Composer", value: [ "SONGWRITER", "COMPOSER" ] },
                                            ] }
                                            placeholder="Select the author's role"

                                            style={ { width: "100%", marginRight: "10px" } }
                                        />
                                        <Button
                                            color="red"
                                            icon
                                            size="tiny"
                                            onClick={ () => this.removeAuthor( i ) }
                                        >
                                            <Icon name="delete" />

                                        </Button>

                                    </Form.Input>
                                </Form.Group>

                            ) )}

                            <Button
                                className="primary"
                                icon
                                onClick={ this.addAuthor }
                            >
                                <Icon name="plus" />

                            </Button>
                        </Form>
                    </Segment>
                </SegmentGroup>
                <SegmentGroup>
                    <Segment>
                        <Header as="h4">Publishers</Header>
                    </Segment>
                    <Segment className="form-body">
                        <Form>
                            {publishers.map( ( publisher, i ) => (
                                <Form.Group key={ publisher.id } widths="equal">
                                    <Form.Input
                                        required
                                        label="Name"
                                    >
                                        <Search
                                            style={ { width: "100%" } }
                                            placeholder="Name or email"
                                            defaultValue={ publisher.name }
                                            onResultSelect={ ( e, data ) => this.setOwner( i, data.result.value, "publisher" ) }
                                            disabled={ account.id === publisher.id }
                                            onSearchChange={ ( e, data ) => this.handleSearchAccount( e, data, "publishers" ) }
                                            resultRenderer={ this.renderOwnerSearchResult }
                                            selectFirstResult
                                            results={ publisherSearchResults.length > 0
                                                ? publisherSearchResults.map( a => ( { title: `${ a.publisher.name }`, value: a } ) )
                                                : [ { title: `Invite: ${ ownerSearchQuery }`, value: { id: ownerSearchQuery } } ] }

                                        />
                                    </Form.Input>
                                    {publisher.country && (
                                        <Form.Input
                                            label="Country"
                                            placeholder="Country"
                                            value={ publisher.country }
                                        />
                                    )}
                                    <Form.Input
                                        label="ISNI"
                                        placeholder="ISNI"
                                        value={ publisher.isni }
                                    />
                                    <Form.Input
                                        label="IPI"
                                        placeholder="IPI"
                                        value={ publisher.ipi }
                                    />
                                    <Form.Input
                                        label="CAE"
                                        placeholder="CAE"
                                        value={ publisher.cae }
                                    />
                                    <Form.Input
                                        label="Contact"
                                    >
                                        <Input
                                            style={ { width: "100%", marginRight: "10px" } }
                                            value={ publisher.contact }
                                            placeholder="Contact"
                                        />
                                        <Button
                                            color="red"
                                            icon
                                            size="tiny"
                                            onClick={ () => this.removePublisher( i ) }
                                        >
                                            <Icon name="delete" />

                                        </Button>

                                    </Form.Input>

                                </Form.Group>
                            ) )}

                            <Button
                                className="primary"
                                icon
                                onClick={ this.addPublisher }
                            >
                                <Icon name="plus" />

                            </Button>
                        </Form>
                    </Segment>
                </SegmentGroup>
                <SegmentGroup>
                    <Segment>
                        <Header as="h4">Splits</Header>
                    </Segment>
                    <Segment>

                        <Form>
                            <Form.Group>
                                <Form.Field>
                                    <Radio
                                        label="Equal split"
                                        value="equal"
                                        checked={ splitType === "equal" }
                                        onChange={ this.handleChangeSplitType }
                                    />
                                </Form.Field>

                                <Form.Field>
                                    <Radio
                                        label="Custom split"
                                        value="custom"
                                        checked={ splitType === "custom" }
                                        onChange={ this.handleChangeSplitType }
                                    />
                                </Form.Field>
                            </Form.Group>
                            <Form.Group>
                                <Form.Field>
                                    <MultiSlider
                                        onChange={ ( values ) => {
                                            if ( splitType === "custom" ) {
                                                values.forEach( ( split, i ) => {
                                                    const id = Array.from( splits.keys() )[ i ];
                                                    splits.set( id, split );
                                                } );
                                                this.setState( { splits } );
                                            }
                                        } }
                                        width={ 400 }
                                        colors={ palette.slice( 0, splits.size ) }
                                        values={ Array.from( splits.values() ) }
                                        trackSize={ 20 }
                                    />

                                </Form.Field>
                                <Form.Field>
                                    <Chart
                                        width="500px"
                                        height="300px"
                                        chartType="PieChart"
                                        loader={ <div>Loading</div> }
                                        data={
                                            pieSplits
                                        }
                                        options={ {
                                            title: "",
                                            slices,
                                        } }
                                        rootProps={ { "data-testid": "1" } }
                                    />
                                </Form.Field>
                            </Form.Group>
                        </Form>
                    </Segment>
                </SegmentGroup>
                <SegmentGroup>
                    <Segment>
                        <Header as="h4">Territories</Header>
                    </Segment>
                    <Segment className="form-body">
                        <Form>
                            <Segment>
                                <Form.Group>
                                    <Form.Dropdown
                                        label="Created in"
                                        required
                                        multiple
                                        placeholder="Select one or more"
                                        onChange={ ( e, data ) => {
                                            this.setState( { createdIn: data.value } );
                                        } }
                                        selection
                                        options={ countries }
                                    />
                                </Form.Group>
                            </Segment>
                            <Segment>
                                <Form.Group widths="equal">
                                    <Form.Input
                                        required
                                        label="Represent this musical work in"
                                    >
                                        <div style={ { display: "block", width: "100%" } }>
                                            <div className="select-all-checkbox">
                                                <Form.Field>
                                                    <Radio
                                                        label="Worldwide"
                                                        name="radioGroup"
                                                        defaultChecked
                                                        checked={ !selectCountries }
                                                        onChange={ ( e, data ) => {
                                                            if ( data.checked ) {
                                                                this.setState( { selectCountries: false, territories: countries.map( c => c.value ) } );
                                                            } else {
                                                                this.setState( { selectCountries: true, territories: [] } );
                                                            }
                                                        } }
                                                    />
                                                </Form.Field>
                                                <Form.Field>
                                                    <Radio
                                                        label="Select countries"
                                                        name="radioGroup"
                                                        checked={ selectCountries }
                                                        onChange={ ( e, data ) => {
                                                            if ( data.checked ) {
                                                                this.setState( { selectCountries: true, territories: [] } );
                                                            }
                                                        } }
                                                    />
                                                </Form.Field>
                                            </div>
                                            <div className="country-checkboxes">
                                                {selectCountries && countries.map( c => (
                                                    <Form.Checkbox
                                                        key={ c.value }
                                                        checked={ territories.includes( c.value ) }
                                                        label={ c.text }
                                                        onChange={ ( e, data ) => {
                                                            if ( data.checked ) {
                                                                this.setState( { territories: [ ...territories, c.value ] } );
                                                            } else {
                                                                this.setState( { territories: territories.filter( t => t !== c.value ) } );
                                                            }
                                                        } }
                                                    >
                                                    </Form.Checkbox>
                                                ) )}
                                            </div>

                                        </div>

                                    </Form.Input>

                                </Form.Group>

                            </Segment>
                        </Form>
                    </Segment>
                </SegmentGroup>
                <Button
                    className={ classnames( this.isFormValid() ? "primary" : "" ) }
                    icon
                    disabled={ !this.isFormValid() }
                    labelPosition="right"
                    loading={ registerWorkProcessing }
                    onClick={ this.register }
                >
      Confirm
                    <Icon name="right arrow" />
                </Button>
            </div>
        );
    }
}
RegisterWorkForm.propTypes = {
    onRegister: func.isRequired,
    searchAccounts: func.isRequired,
    registerWorkProcessing: bool.isRequired,
    account: shape( {} ).isRequired,
    authors: arrayOf(),
    publishers: arrayOf(),
};
RegisterWorkForm.defaultProps = {
    authors: [],
    publishers: [],
};
export default RegisterWorkForm;
