import React from "react";
import { VerificationRequest } from "components";
import {
    arrayOf, shape, func, bool,
} from "prop-types";

const RequestsTab = ( {
    workVerificationRequests, recordingVerificationRequests, verify, verifying, account,
} ) => (
    <div className="page-content">
        {account && recordingVerificationRequests && recordingVerificationRequests.filter( request => !request.verifiedBy.find( entry => entry.account.id === account.id ) ).map( ( r, index ) => (
            <VerificationRequest
                collapsed
                type="Recording"
                verificationRequest={ r }
                verifying={ verifying }
                onVerify={ () => {
                    verify( { id: r.id, type: "recordings" } );
                } }
            />
        ) )}
        {account && workVerificationRequests && workVerificationRequests.filter( request => !request.verifiedBy.find( entry => entry.account.id === account.id ) ).map( ( r, index ) => (
            <VerificationRequest
                collapsed
                type="Work"
                verificationRequest={ r }
                verifying={ verifying }
                onVerify={ () => {
                    verify( { id: r.id, type: "works" } );
                } }
            />
        ) )}

    </div>
);
RequestsTab.propTypes = {
    workVerificationRequests: arrayOf( shape( {} ) ).isRequired,
    recordingVerificationRequests: arrayOf( shape( {} ) ).isRequired,
    verify: func.isRequired,
    verifying: bool.isRequired,
};

export default RequestsTab;
