/* eslint-disable complexity */
/* eslint-disable react/no-array-index-key */
import {
    arrayOf, func, string,
} from "prop-types";
import React from "react";
import {
    Table, Dropdown,
} from "semantic-ui-react";

const generateHeaderForType = ( type, onChangeView ) => {
    let headers = [];
    if ( type === "works" ) {
        headers = [ "Resource ID", "Title", "ISWC", "Authors", "Roles", "Publishers", "Split", "Created in", "Territories" ];
    } else if ( type === "recordings" ) {
        headers = [ "Resource ID", "Title", "Version Title", "ISRC", "Duration", "Artists", "Roles", "Labels", "Split", "Created in", "Territories" ];
    }
    return (
        <Table.Header>
            <Table.Row>
                {headers.map( h => (
                    <Table.HeaderCell key={ h }>
                        {h}
                    </Table.HeaderCell>
                ) )}

                <Table.HeaderCell>
                    <Dropdown text="View">
                        <Dropdown.Menu>
                            <Dropdown.Item
                                text="Styled"
                                onClick={ () => onChangeView( { view: "styled" } ) }
                            />
                            <Dropdown.Item
                                text="Raw"
                                onClick={ () => onChangeView( { view: "raw" } ) }
                            />
                        </Dropdown.Menu>
                    </Dropdown>
                </Table.HeaderCell>
            </Table.Row>
        </Table.Header>

    );
};
const propertyIndexMap = {
    works: {
        resourceId: 0,
        title: 1,
        iswc: 2,
        author: 3,
        role: 4,
        publisher: 5,
        split: 6,
        createdIn: 7,
        territories: 8,
    },
    recordings: {
        resourceId: 0,
        title: 1,
        versionTitle: 2,
        isrc: 3,
        duration: 4,
        artist: 5,
        role: 6,
        label: 7,
        split: 8,
        createdIn: 9,
        territories: 10,
    },

};

const generateBodyForType = ( type, data ) => data.map( ( row, index ) => {
    try {
        if ( type === "works" ) {
            const resourceId = row[ propertyIndexMap.works.resourceId ];
            const title = row[ propertyIndexMap.works.title ];
            const iswc = row[ propertyIndexMap.works.iswc ];
            const author = row[ propertyIndexMap.works.author ];
            const role = row[ propertyIndexMap.works.role ];
            const publisher = row[ propertyIndexMap.works.publisher ];
            const split = row[ propertyIndexMap.works.split ];
            const createdIn = row[ propertyIndexMap.works.createdIn ];
            const territories = row[ propertyIndexMap.works.territories ];
            return (
                <Table.Row key={ `work ${ author }${ resourceId }` || publisher + resourceId }>
                    <Table.Cell>{resourceId}</Table.Cell>
                    <Table.Cell>{title}</Table.Cell>
                    <Table.Cell>{iswc}</Table.Cell>
                    <Table.Cell>{author}</Table.Cell>
                    <Table.Cell>{role}</Table.Cell>
                    <Table.Cell>{publisher}</Table.Cell>
                    <Table.Cell>{split}</Table.Cell>
                    <Table.Cell>{createdIn}</Table.Cell>
                    <Table.Cell>{territories}</Table.Cell>
                    <Table.Cell></Table.Cell>
                </Table.Row>
            );
        }
        if ( type === "recordings" ) {
            const resourceId = row[ propertyIndexMap.recordings.resourceId ];
            const title = row[ propertyIndexMap.recordings.title ];
            const versionTitle = row[ propertyIndexMap.recordings.versionTitle ];
            const isrc = row[ propertyIndexMap.recordings.isrc ];
            const duration = row[ propertyIndexMap.recordings.duration ];
            const artist = row[ propertyIndexMap.recordings.artist ];
            const role = row[ propertyIndexMap.recordings.role ];
            const label = row[ propertyIndexMap.recordings.label ];
            const split = row[ propertyIndexMap.recordings.split ];
            const createdIn = row[ propertyIndexMap.recordings.createdIn ];
            const territories = row[ propertyIndexMap.recordings.territories ];
            return (
                <Table.Row key={ `recording ${ artist }${ resourceId }` || label + resourceId }>
                    <Table.Cell>{resourceId}</Table.Cell>
                    <Table.Cell>{title}</Table.Cell>
                    <Table.Cell>{versionTitle}</Table.Cell>
                    <Table.Cell>{isrc}</Table.Cell>
                    <Table.Cell>{duration}</Table.Cell>
                    <Table.Cell>{artist}</Table.Cell>
                    <Table.Cell>{role}</Table.Cell>
                    <Table.Cell>{label}</Table.Cell>
                    <Table.Cell>{split}</Table.Cell>
                    <Table.Cell>{createdIn}</Table.Cell>
                    <Table.Cell>{territories}</Table.Cell>
                    <Table.Cell></Table.Cell>
                </Table.Row>
            );
        }
    } catch ( error ) {
        return (
            <Table.Row key={ `row${ index }` }>
                <Table.Cell>invalid</Table.Cell>
            </Table.Row>
        );
    }
} );
const RawOutput = ( {
    onChangeView, data, type,
} ) => (
    <Table basic>
        {generateHeaderForType( type, onChangeView )}
        <Table.Body>
            {generateBodyForType( type, data )}
        </Table.Body>

        <Table.Footer></Table.Footer>
    </Table>

);
RawOutput.propTypes = {
    onChangeView: func.isRequired,
    data: arrayOf( ).isRequired,
    type: string,
};
RawOutput.defaultProps = {
    type: "works",
};
export default RawOutput;
