import { combineReducers } from "redux";

// Imported reducers
import { reducers as app } from "./app/duck";
import { reducers as catalogue } from "./pages/catalogue/duck";
import { reducers as profile } from "./pages/profile/duck";
import { reducers as registerSongs } from "./pages/register-songs/duck";
import { reducers as verifications } from "./pages/verifications/duck";
import { reducers as distribution } from "./pages/distribution/duck";
import { reducers as error } from "./components/error/duck";

const reducers = {
    // Combined reducers
    app,
    catalogue,
    profile,
    registerSongs,
    verifications,
    distribution,
    error,
};

export default combineReducers( reducers );
