import {
    all,
    call,
    put,
    takeEvery,
} from "redux-saga/effects";
import axios from "axios";

import errorSelectors from "components/error/duck/Selectors";
import errorTypes from "components/error/duck/Types";
import types from "./Types";

function* init() {
    const env = process.env.REACT_APP_ENV_NAME;
    const health = process.env[ `REACT_APP_HEALTH_URL_${ env }` ];

    try {
        yield all( [
            call( axios.get, `${ health }` ),
        ] );
        yield call( getVerificationRequestCount );
        yield put( { type: types.VALIDATE_CONNECTION_SUCCESS } );
    } catch ( error ) {
        yield put( { type: types.VALIDATE_CONNECTION_ERROR, error } );
    }
}
function* getVerificationRequestCount() {
    try {
        const { data: worksCount = 0 } = yield call( axios.get, "/works/unverified/count" );
        const { data: recordingsCount = 0 } = yield call( axios.get, "/recordings/unverified/count" );
        const { data: pendingWorksCount = 0 } = yield call( axios.get, "/works/pending/count" );
        const { data: pendingRecordingsCount = 0 } = yield call( axios.get, "/recordings/pending/count" );

        yield put( {
            type: types.GET_VERIFICATIONS_COUNT_SUCCESS,
            payload: {
                unverified: worksCount + recordingsCount,
                pending: pendingWorksCount + pendingRecordingsCount,
            },
        } );
    // eslint-disable-next-line no-empty
    } catch ( error ) {

    }
}
function* login( { payload } ) {
    try {
        const { data } = yield call( axios.post, "/accounts/auth", payload );

        yield put( { type: types.LOGIN_SUCCESS, payload: data } );
        yield call( getProfile );
    } catch ( error ) {
        yield put( {
            type: errorSelectors.errorType( error, types.LOGIN_ERROR, errorTypes.APP_ERROR ),
            payload: errorSelectors.errorPayload( error ),
        } );
    }
}
function* register( { payload } ) {
    try {
        const { data } = yield call( axios.post, "/accounts", payload );
        yield put( { type: types.REGISTER_SUCCESS, payload: data } );
        yield call( getProfile );
    } catch ( error ) {
        yield put( {
            type: errorSelectors.errorType( error, types.REGISTER_ERROR, errorTypes.APP_ERROR ),
            payload: errorSelectors.errorPayload( error ),
        } );
    }
}
function* getProfile( ) {
    try {
        const { data } = yield call( axios.get, "/accounts/me" );
        yield put( { type: types.GET_PROFILE_SUCCESS, payload: data } );
    } catch ( error ) {
        yield put( {
            type: errorSelectors.errorType( error, types.GET_PROFILE_ERROR, errorTypes.APP_ERROR ),
            payload: errorSelectors.errorPayload( error ),
        } );
    }
}

function* authenticateMobileConnect( { payload } ) {
    try {
        const { code, state } = payload;
        const { data } = yield call( axios.get, `/verifications/mobileconnect?code=${ code }&state=${ state }` );

        yield put( { type: types.AUTH_MOBILECONNECT_SUCCESS, payload: data } );
    } catch ( error ) {
        yield put( {
            type: errorSelectors.errorType( error, types.AUTH_MOBILECONNECT_ERROR, errorTypes.APP_ERROR ),
            payload: errorSelectors.errorPayload( error ),
        } );
    }
}
export default function* main() {
    yield takeEvery( types.INIT, init );
    yield takeEvery( types.GET_PROFILE, getProfile );
    yield takeEvery( types.LOGIN, login );
    yield takeEvery( types.REGISTER, register );
    yield takeEvery( types.AUTH_MOBILECONNECT, authenticateMobileConnect );
}
