/* eslint-disable no-mixed-operators */
/* eslint-disable complexity */
import React from "react";
import {
    Chart,
} from "react-google-charts";
import moment from "moment";
import {
    Segment, SegmentGroup, Loader,
} from "semantic-ui-react";
import { string } from "prop-types";

const SIZE = 22;
const getDays = () => {
    const days = [ [ "Country", "Listeners" ] ];
    for ( let i = 1; i < SIZE; i += 1 ) {
        const day = moment().add( -( SIZE - 1 ) + i, "day" ).format( "ddd D" );
        days[ i ] = [ day, Math.floor( Math.random() * 100000 ) + 1 ];
    }
    return days;
};

const getWeeks = () => {
    const weeks = [ [ "Country", "Listeners" ] ];
    for ( let i = 1; i < SIZE; i += 1 ) {
        const week = moment().add( -( SIZE - 1 ) + i, "week" ).format( "W" );
        weeks[ i ] = [ week, Math.floor( Math.random() * 100000 ) + 1 ];
    }
    return weeks;
};

const getMonths = () => {
    const months = [ [ "Country", "Listeners" ] ];
    for ( let i = 1; i < SIZE; i += 1 ) {
        const month = moment().add( -( SIZE - 1 ) + i, "month" ).format( "MMM" );
        months[ i ] = [ month, Math.floor( Math.random() * 100000 ) + 1 ];
    }
    return months;
};

const getYears = () => {
    const years = [ [ "Country", "Listeners" ] ];
    for ( let i = 1; i < SIZE; i += 1 ) {
        const year = moment().add( -( SIZE - 1 ) + i, "year" ).format( "YYYY" );
        years[ i ] = [ year, Math.floor( Math.random() * 100000 ) + 1 ];
    }
    return years;
};
const PlaysChart = ( { period } ) => (
    <SegmentGroup>
        <Segment>
Plays per
            {" "}
            {period}
        </Segment>
        <Segment>
            <Chart

                width="1300px"
                height="300px"
                chartType="ColumnChart"
                loader={ <Loader active inline="centered" /> }
                data={
                    period === "day" && getDays()
                    || period === "week" && getWeeks()
                    || period === "month" && getMonths()
                    || period === "year" && getYears()
                }
                options={ {
                    title: "",
                    chartArea: { width: "80%" },
                    isStacked: false,
                    animation: {
                        duration: 1000,
                        easing: "out",
                        startup: true,
                    },
                    colors: [ "#4B13A3", "#42d63a" ],

                } }
                // For tests
                rootProps={ { "data-testid": "3" } }
            />
        </Segment>
    </SegmentGroup>
);
PlaysChart.propTypes = {
    period: string.isRequired,
};
export default PlaysChart;
