/* eslint-disable complexity */
import { combineReducers } from "redux";

import types from "./Types";

const defaultState = {
    recordings: [],
    errors: {},
    masterFilePath: null,
    masterFileProgress: 0,
};

const recordings = ( state = defaultState.recordings, { type, payload } ) => {
    if ( type === types.LOAD_RECORDINGS_SUCCESS ) {
        if ( !payload ) {
            return [];
        }

        return payload.recordings;
    }
    return state;
};

const masterFilePath = ( state = defaultState.masterFilePath, { type, payload } ) => {
    if ( type === types.DISTRIBUTE_SUCCESS ) {
        return payload;
    }
    return state;
};

const masterFileProgress = ( state = defaultState.masterFileProgress, { type, payload } ) => {
    if ( type === types.DISTRIBUTE_SUCCESS ) {
        return 100;
    }
    if ( type === types.DISTRIBUTE_PROGRESS ) {
        return payload;
    }
    if ( type === types.DISTRIBUTE || type === types.DISTRIBUTE_ERROR ) {
        return 0;
    }
    return state;
};

const errors = ( state = defaultState.errors, { type, payload } ) => {
    if ( type === types.LOAD_WORKS ) {
        return {};
    }
    if ( type === types.LOAD_WORKS_ERROR ) {
        return payload;
    }
    return state;
};

export default combineReducers( {
    recordings,
    masterFilePath,
    masterFileProgress,
    errors,
} );
