import React from "react";
import {
    Table, Menu, Icon,
} from "semantic-ui-react";
import { arrayOf, shape } from "prop-types";

const WorksTab = ( { works } ) => (
    <div className="page-content">
        <div className="ui icon input" style={ { width: "100%" } }>

            <i aria-hidden="true" className="search icon" />
            <input type="text" tabIndex="0" className="prompt" autoComplete="off" value="" placeholder="Search works.." />
        </div>
        <Table basic>
            <Table.Header>
                <Table.Row>

                    <Table.HeaderCell>
               ISWC
                    </Table.HeaderCell>
                    <Table.HeaderCell>
              Title
                    </Table.HeaderCell>

                    <Table.HeaderCell>
                Authors
                    </Table.HeaderCell>
                    <Table.HeaderCell>
                Publishers
                    </Table.HeaderCell>
                    <Table.HeaderCell>Representatives</Table.HeaderCell>

                    <Table.HeaderCell>ISRCs</Table.HeaderCell>

                    <Table.HeaderCell>IPIs</Table.HeaderCell>

                    <Table.HeaderCell>ISNIs</Table.HeaderCell>

                </Table.Row>
            </Table.Header>

            <Table.Body>
                {works.map( w => (
                    <Table.Row key={ w.id }>
                        <Table.Cell>
                            {w.iswc}
                        </Table.Cell>
                        <Table.Cell>
                            {w.title}
                        </Table.Cell>
                        <Table.Cell>
                            {w.authors.map( ( a, index ) => {
                                if ( index === w.authors.length - 1 ) {
                                    return `${ a.firstname } ${ a.lastname }`;
                                }
                                return `${ a.firstname } ${ a.lastname } , `;
                            } )}
                        </Table.Cell>
                        <Table.Cell>
                            {w.publishers.map( ( a, index ) => {
                                if ( index === w.publishers.length - 1 ) {
                                    return `${ a.publisher.name }`;
                                }
                                return `${ a.publisher.name }, `;
                            } )}
                        </Table.Cell>
                        <Table.Cell>

                        </Table.Cell>
                        <Table.Cell>
                            {w.isrc}
                        </Table.Cell>
                        <Table.Cell>

                        </Table.Cell>
                        <Table.Cell>

                        </Table.Cell>
                        <Table.Cell>

                        </Table.Cell>
                    </Table.Row>
                ) )}
            </Table.Body>

            <Table.Footer>
                <Table.Row>
                    <Table.HeaderCell colSpan="12">
                        <Menu floated="left" pagination id="table-footer">
                            <Menu.Item as="a" icon>
                                <Icon name="chevron left" />
                            </Menu.Item>
                            <Menu.Item as="a"><span>1</span></Menu.Item>
                            <Menu.Item as="a" icon>
                                <Icon name="chevron right" />
                            </Menu.Item>
                        </Menu>
                        <Menu floated="right" id="table-footer-right">

                            <Menu.Item as="a"><span>10</span></Menu.Item>
                            <Menu.Item as="a" disabled><span>/</span></Menu.Item>
                            <Menu.Item as="a"><span>50</span></Menu.Item>
                            <Menu.Item as="a" disabled><span>/</span></Menu.Item>
                            <Menu.Item as="a"><span>100</span></Menu.Item>

                        </Menu>
                    </Table.HeaderCell>
                </Table.Row>
            </Table.Footer>

        </Table>
    </div>
);
WorksTab.propTypes = {
    works: arrayOf( shape( {} ) ).isRequired,
};
export default WorksTab;
