import React from "react";
import {
    Table, Menu, Icon,
} from "semantic-ui-react";
import { arrayOf, shape } from "prop-types";

const RecordingsTab = ( { recordings } ) => (
    <div className="page-content">
        <div className="ui icon input" style={ { width: "100%" } }>

            <i aria-hidden="true" className="search icon" />
            <input type="text" tabIndex="0" className="prompt" autoComplete="off" value="" placeholder="Search recordings.." />
        </div>
        <Table basic>
            <Table.Header>
                <Table.Row>
                    <Table.HeaderCell>
              ISRC
                    </Table.HeaderCell>
                    <Table.HeaderCell>
               Title
                    </Table.HeaderCell>

                    <Table.HeaderCell>
                Credited artist
                    </Table.HeaderCell>
                    <Table.HeaderCell>
                Recording artists
                    </Table.HeaderCell>
                    <Table.HeaderCell>
                Duration
                    </Table.HeaderCell>
                    <Table.HeaderCell>
                Label names
                    </Table.HeaderCell>
                    <Table.HeaderCell>
                Label year
                    </Table.HeaderCell>

                    <Table.HeaderCell>IPIs</Table.HeaderCell>
                    <Table.HeaderCell>ISNIs</Table.HeaderCell>
                    <Table.HeaderCell>ISWCs</Table.HeaderCell>
                    <Table.HeaderCell>ISRCs</Table.HeaderCell>
                    <Table.HeaderCell>Derivative ISRCs</Table.HeaderCell>
                </Table.Row>
            </Table.Header>

            <Table.Body>
                {recordings.map( r => (
                    <Table.Row>

                        <Table.Cell>
                            {r.isrc}
                        </Table.Cell>
                        <Table.Cell>

                            {r.title}

                        </Table.Cell>
                        <Table.Cell>
                            {r.creditedArtist}
                        </Table.Cell>
                        <Table.Cell>
                            {r.additionalArtists.map( ( a, index ) => {
                                if ( index === r.additionalArtists.length - 1 ) {
                                    return a.artist.name;
                                }
                                return `${ a.artist.name }, `;
                            } )}
                        </Table.Cell>
                        <Table.Cell>
                            {r.duration}
                        </Table.Cell>
                        <Table.Cell>
                            {r.labels.map( ( a, index ) => {
                                if ( index === r.labels.length - 1 ) {
                                    return a.label.name;
                                }
                                return `${ a.label.name }, `;
                            } )}
                        </Table.Cell>
                        <Table.Cell>
                        </Table.Cell>
                        <Table.Cell>

                        </Table.Cell>
                        <Table.Cell>

                        </Table.Cell>
                        <Table.Cell>

                        </Table.Cell>
                        <Table.Cell>

                        </Table.Cell>
                        <Table.Cell>

                        </Table.Cell>

                    </Table.Row>
                ) )}

            </Table.Body>

            <Table.Footer>
                <Table.Row>
                    <Table.HeaderCell colSpan="12">
                        <Menu floated="left" pagination id="table-footer">
                            <Menu.Item as="a" icon>
                                <Icon name="chevron left" />
                            </Menu.Item>
                            <Menu.Item as="a"><span>1</span></Menu.Item>

                            <Menu.Item as="a" icon>
                                <Icon name="chevron right" />
                            </Menu.Item>
                        </Menu>
                        <Menu floated="right" id="table-footer-right">

                            <Menu.Item as="a"><span>10</span></Menu.Item>
                            <Menu.Item as="a" disabled><span>/</span></Menu.Item>
                            <Menu.Item as="a"><span>50</span></Menu.Item>
                            <Menu.Item as="a" disabled><span>/</span></Menu.Item>
                            <Menu.Item as="a"><span>100</span></Menu.Item>

                        </Menu>
                    </Table.HeaderCell>
                </Table.Row>

            </Table.Footer>

        </Table>

    </div>
);
RecordingsTab.propTypes = {
    recordings: arrayOf( shape( {} ) ).isRequired,
};
export default RecordingsTab;
