import React from "react";
import { connect } from "react-redux";
import { bool, func } from "prop-types";
import {
    Input, Form, Button, Segment,
} from "semantic-ui-react";
import { Redirect } from "react-router-dom";
import { actions as appActions } from "app/duck";

class PageRegister extends React.Component {
    register=() => {
        const { Register } = this.props;
        const {
            email, password, firstname, lastname,
        } = this.state;
        Register( {
            email, password, firstname, lastname,
        } );
    }

    render() {
        const { isAuthenticated, registering } = this.props;
        if ( isAuthenticated ) {
            return <Redirect to="/" />;
        }
        return (
            <div id="login-page" className="ui middle aligned center aligned grid">
                <div className="column three wide">
                    <div className="row">
                        <img src="https://www.copyrightdelta.com/images/Asset-8.svg" id="logo" alt="Copyright Delta" />
                    </div>
                    <Segment>

                        <Form>
                            <Form.Field>
                                <Input
                                    placeholder="Email"
                                    onChange={ ( e ) => {
                                        this.setState( { email: e.currentTarget.value } );
                                    } }
                                />
                            </Form.Field>
                            <Form.Field>
                                <Input
                                    type="password"
                                    placeholder="Password"
                                    onChange={ ( e ) => {
                                        this.setState( { password: e.currentTarget.value } );
                                    } }
                                />
                            </Form.Field>

                            <Form.Field>
                                <Input
                                    placeholder="Firstname"
                                    onChange={ ( e ) => {
                                        this.setState( { firstname: e.currentTarget.value } );
                                    } }
                                />
                            </Form.Field>
                            <Form.Field>
                                <Input
                                    placeholder="Lastname"
                                    onChange={ ( e ) => {
                                        this.setState( { lastname: e.currentTarget.value } );
                                    } }
                                />
                            </Form.Field>
                            <Form.Field>
                                <Button
                                    loading={ registering }
                                    type="submit"
                                    fluid

                                    className="primary"
                                    onClick={ this.register }
                                >
Register
                                </Button>
                            </Form.Field>

                        </Form>
                    </Segment>

                </div>
            </div>

        );
    }
}
PageRegister.propTypes = {
    Register: func.isRequired,
    isAuthenticated: bool.isRequired,
    registering: bool.isRequired,
};
const mSTP = ( {
    app: {
        isAuthenticated,
        registering,
        errors,
    },
} ) => ( {
    isAuthenticated, errors, registering,
} );

const mDTP = dispatch => ( {
    Register: values => dispatch( appActions.register( values ) ),
} );

export default connect( mSTP, mDTP )( PageRegister );
