/* eslint-disable complexity */
import React, { Component } from "react";
import { bool, func, shape } from "prop-types";
import {
    Segment, SegmentGroup, Form, Button, Header,
} from "semantic-ui-react";

import countryList from "iso-3166-country-list";

const countries = countryList.map( country => ( { key: country.code, text: country.name, value: country.code } ) );

class AuthorTab extends Component {
    constructor( props ) {
        super( props );
        this.state = {
            country: null,
            isni: null,
            ipi: null,
            cae: null,
        };
    }

    save=() => {
        const {
            country, isni, ipi, cae,
        } = this.state;
        const { onSave, author } = this.props;
        onSave( {
            country: country || author.country,
            isni: isni || author.isni,
            ipi: ipi || author.ipi,
            cae: cae || author.cae,
        } );
    }

    render() {
        const { author, saving } = this.props;
        const {
            country, isni, ipi, cae,
        } = this.state;
        return (
            <div className="page-content">

                <SegmentGroup>
                    <Segment>

                        <Header as="h4">Author</Header>
                    </Segment>
                    <Segment className="form-body">
                        <Form>
                            <Form.Group widths="equal">
                                <Form.Select
                                    required
                                    label="Country"
                                    value={ author && !country ? author.country : country }
                                    placeholder="Select one"
                                    selection
                                    options={ countries }
                                    onChange={ ( e, { value } ) => {
                                        this.setState( { country: value } );
                                    } }
                                />
                                <Form.Input
                                    label="ISNI"
                                    value={ author && !isni ? author.isni : isni }
                                    placeholder="ISNI"
                                    onChange={ ( e ) => {
                                        this.setState( { isni: e.currentTarget.value } );
                                    } }
                                />
                                <Form.Input
                                    label="IPI"
                                    value={ author && !ipi ? author.ipi : ipi }
                                    placeholder="IPI"
                                    onChange={ ( e ) => {
                                        this.setState( { ipi: e.currentTarget.value } );
                                    } }
                                />
                                <Form.Input
                                    label="CAE"

                                    value={ author && !cae ? author.cae : cae }
                                    placeholder="CAE"
                                    onChange={ ( e ) => {
                                        this.setState( { cae: e.currentTarget.value } );
                                    } }
                                />
                            </Form.Group>

                        </Form>
                    </Segment>
                </SegmentGroup>

                <Button

                    className="primary"
                    loading={ saving }
                    onClick={ this.save }
                >
                    {author ? "Save" : "Confirm"}

                </Button>
            </div>
        );
    }
}
AuthorTab.propTypes = {
    onSave: func.isRequired,
    saving: bool.isRequired,
    author: shape( {} ),
};
AuthorTab.defaultProps = {
    author: null,
};
export default AuthorTab;
