/* eslint-disable react/prefer-stateless-function */
/* eslint-disable complexity */
import React from "react";
import { connect } from "react-redux";
import { bool, shape } from "prop-types";
import {
    Menu, Dropdown, Image,
} from "semantic-ui-react";
import { withRouter, NavLink } from "react-router-dom";

class Navbar extends React.Component {
    getImageUrl=() => {
        const {
            account,
        } = this.props;

        const placeholder = "https://autototaalbv.nl/wp-content/uploads/2017/04/profile-placeholder.png";
        if ( !account ) {
            return placeholder;
        }
        if ( account.artist ) {
            return account.artist.image || placeholder;
        }
        if ( account.author ) {
            return account.author.image || placeholder;
        }
        if ( account.publisher ) {
            return account.publisher.image || placeholder;
        }
        if ( account.label ) {
            return account.label.image || placeholder;
        }
        return placeholder;
    }

    render() {
        const { location, isAuthenticated } = this.props;
        return (
            <Menu id="navbar">
                <Menu.Menu position="left">
                    <Menu.Item
                        id="navbar-title"
                        name="signup"
                    >
                        {Navbar.getTitle( location )}
                    </Menu.Item>

                </Menu.Menu>
                <Menu.Menu position="right">
                    {isAuthenticated
                    && (
                        <Menu.Item id="navbar-account">

                            <Image src={ this.getImageUrl() } avatar />
                            <Dropdown text="My account">
                                <Dropdown.Menu>
                                    <Dropdown.Item text="My profile" as={ NavLink } to="/profile" />
                                    <Dropdown.Item text="Settings" as={ NavLink } to="/settings" />
                                    <Dropdown.Item text="Logout" as={ NavLink } to="/login?logout=true" />
                                </Dropdown.Menu>
                            </Dropdown>
                        </Menu.Item>
                    )}
                </Menu.Menu>
            </Menu>
        );
    }
}
Navbar.getTitle = ( location ) => {
    const { pathname } = location;
    switch ( pathname ) {
        case "/catalogue":
            return "Catalogue";
        case "/profile":
            return "Profile";
        case "/dashboard":
            return "Dashboard";
        case "/approvals":
            return "Approvals";
        case "/registrations":
            return "Registration";
        case "/partners":
            return "Partners";
        case "/distribution":
        case "/distribution/single":
        case "/distribution/album":
            return "Distribution";
        case "/splits":
            return "Royalty Splits";
        case "/settings":
            return "Settings";
        default:
            return "Undefined";
    }
};
Navbar.propTypes = {
    isAuthenticated: bool.isRequired,
    location: shape( {} ).isRequired,
    account: shape( {} ).isRequired,
};

const mSTP = ( { app: { isAuthenticated, account } } ) => ( { isAuthenticated, account } );

export default connect( mSTP )( withRouter( Navbar ) );
