/* eslint-disable complexity */
import React from "react";
import { withRouter, Link } from "react-router-dom";
import {
    Sidebar, Menu, Image, Label,
} from "semantic-ui-react";
import classnames from "classnames";
import {
    string, shape, number, arrayOf,
} from "prop-types";

const MySidebar = ( {
    pendingVerificationRequestsCount, verificationRequestsCount, roles, currentParty, location: { pathname },
} ) => {
    const colors = MySidebar.getColours( currentParty );
    return (
        <Sidebar
            id="sidebar"
            as={ Menu }
            animation="overlay"
            icon="labeled"
            vertical
            visible
            style={ { background: `transparent linear-gradient(180deg, ${ colors.primary } 0%, ${ colors.secondary } 100%) 0% 0% no-repeat padding-box` } }

        >
            <div className="sidebar-header"><Image src={ MySidebar.getPartyLogo( currentParty ) } alt="Copyright Delta" /></div>
            {roles.some( r => r === "AUTHOR" || r === "ARTIST" || r === "PUBLISHER" || r === "LABEL" )
&& (
    <Menu.Item className={ classnames( pathname === "/dashboard" && "selected" ) } as={ Link } to="/dashboard">
           Dashboard
    </Menu.Item>
)}
            <Menu.Item className={ classnames( pathname === "/catalogue" && "selected" ) } as={ Link } to="/catalogue">
            Catalogue
            </Menu.Item>
            {roles.some( r => r === "ARTIST" || r === "LABEL" )
&& (
    <Menu.Item className={ classnames( pathname.startsWith( "/distribution" ) && "selected" ) } as={ Link } to="/distribution">
        Distribution
    </Menu.Item>
)}
            {roles.some( r => r === "AUTHOR" || r === "ARTIST" || r === "PUBLISHER" || r === "LABEL" )
&& (
    <Menu.Item className={ classnames( pathname === "/splits" && "selected" ) } as={ Link } to="/splits">
        Splits
    </Menu.Item>
)}

            {roles.some( r => r === "AUTHOR" || r === "ARTIST" )

            && (
                <Menu.Item className={ classnames( pathname === "/registrations" && "selected" ) } as={ Link } to="/registrations">
        Register
                </Menu.Item>
            )}
            {roles.some( r => r === "AUTHOR" || r === "ARTIST" || r === "PUBLISHER" || r === "LABEL" )
&& (
    <Menu.Item className={ classnames( pathname === "/approvals" && "selected" ) } as={ Link } to="/approvals">
        Approvals

        {verificationRequestsCount > 0 && (
            <Label circular color="red">
                {verificationRequestsCount}
            </Label>
        )}
        {pendingVerificationRequestsCount > 0 && (
            <Label circular color="orange">
                {pendingVerificationRequestsCount}
            </Label>
        )}
    </Menu.Item>
)}

        </Sidebar>

    );
};
MySidebar.getPartyLogo = ( party ) => {
    switch ( party ) {
        case "Copyright Delta":
            return "https://www.copyrightdelta.com/images/Asset-8.svg";
        case "Red Bull Media House":
            return "https://www.redbullmediahouse.com/5ceec2709f9d18abe0b58dcc03658f0a.svg";
        case "Warner Music Benelux":
            return "https://assets.website-files.com/5edf8c91fb143f7c6aab7318/5ee9f9f7b0250f405b3fd6d7_WGM_LOGO_BLACK-02.svg";
        default: return "";
    }
};
MySidebar.getColours = ( party ) => {
    switch ( party ) {
        case "Copyright Delta":
            return { primary: "#4B13A3", secondary: "#02053B " };
        case "Red Bull Media House":
            return { primary: "white", secondary: "#e93b61 " };
        case "Warner Music Benelux":
            return { primary: "#edefe2", secondary: "#edefe2 " };
        default: return "";
    }
};
MySidebar.propTypes = {
    currentParty: string,
    location: shape( {} ).isRequired,
    roles: arrayOf( string ).isRequired,
    verificationRequestsCount: number,
};

MySidebar.defaultProps = {
    currentParty: "Copyright Delta",
    verificationRequestsCount: 0,
};

export default withRouter( MySidebar );
