/* eslint-disable complexity */
import { combineReducers } from "redux";

import types from "./Types";

const defaultState = {
    errors: {},
    saving: false,
    author: null,
    publisher: null,
    label: null,
    artist: null,
};

const author = ( state = defaultState.author, { type, payload } ) => {
    if ( type === types.LOAD_SUCCESS || type === types.SET_AUTHOR_PROFILE_SUCCESS ) {
        return payload.author;
    }
    return state;
};
const publisher = ( state = defaultState.publisher, { type, payload } ) => {
    if ( type === types.LOAD_SUCCESS || type === types.SET_PUBLISHER_PROFILE_SUCCESS ) {
        return payload.publisher;
    }
    return state;
};
const label = ( state = defaultState.label, { type, payload } ) => {
    if ( type === types.LOAD_SUCCESS || type === types.SET_LABEL_PROFILE_SUCCESS ) {
        return payload.label;
    }
    return state;
};
const artist = ( state = defaultState.artist, { type, payload } ) => {
    if ( type === types.LOAD_SUCCESS || type === types.SET_ARTIST_PROFILE_SUCCESS ) {
        return payload.artist;
    }
    return state;
};
const saving = ( state = defaultState.saving, { type } ) => {
    if ( type === types.SET_AUTHOR_PROFILE ) {
        return true;
    }
    if ( type === types.SET_AUTHOR_PROFILE_SUCCESS || type === types.SET_AUTHOR_PROFILE_ERROR ) {
        return false;
    }
    return state;
};
export default combineReducers( {
    label, artist, publisher, author, saving,
} );
