import React from "react";

import { actions as appActions } from "app/duck";
import { Page } from "components";
import { func, shape } from "prop-types";
import {
    Tab,
} from "semantic-ui-react";
import { connect } from "react-redux";
import { actions } from "./duck";
import RecordingsTab from "./RecordingsTab";
import WorksTab from "./WorksTab";
import BatchTab from "./batch/BatchTab";

const panes = ( {
    account,
} ) => {
    const tabs = [];
    if ( account.author ) {
        tabs.push( {
            menuItem: "Works",
            render: () => (
                <Tab.Pane>
                    <WorksTab />
                </Tab.Pane>
            ),
        } );
    }
    if ( account.artist ) {
        tabs.push( {
            menuItem: "Recordings",
            render: () => (
                <Tab.Pane>
                    <RecordingsTab />
                </Tab.Pane>
            ),
        } );
    }
    tabs.push( {
        menuItem: "Batch",
        render: () => (
            <Tab.Pane>
                <BatchTab account={ account } />
            </Tab.Pane>
        ),
    } );

    return tabs;
};
// eslint-disable-next-line react/prefer-stateless-function
class PageRegisterSongs extends React.Component {
    componentDidMount() {
        const { GetProfile } = this.props;
        GetProfile();
    }

    render() {
        const { account } = this.props;
        return (
            <Page>

                {account && (
                    <Tab
                        panes={ panes( {
                            account,
                        } ) }
                        className="tabs"
                        menu={ { secondary: true, pointing: true } }
                    />
                )}

            </Page>
        );
    }
}
PageRegisterSongs.propTypes = {
    GetProfile: func.isRequired,
    account: shape( {} ),
};
PageRegisterSongs.defaultProps = {
    account: null,
};
const mSTP = ( {
    app: { account },
} ) => ( {
    account,
} );

const mDTP = dispatch => ( {
    GetProfile: () => dispatch( appActions.getProfile() ),
    RegisterWork: payload => dispatch( actions.registerWork( payload ) ),
    Reset: payload => dispatch( actions.reset( payload ) ),
    RegisterRecording: payload => dispatch( actions.registerRecording( payload ) ),
    SearchAccounts: payload => dispatch( actions.searchAccounts( payload ) ),
} );

export default connect( mSTP, mDTP )( PageRegisterSongs );
