/* eslint-disable complexity */
import { combineReducers } from "redux";

import types from "./Types";

const defaultState = {
    works: [],
    recordings: [],
    errors: {},
};

const recordings = ( state = defaultState.recordings, { type, payload } ) => {
    if ( type === types.LOAD_RECORDINGS_SUCCESS ) {
        if ( !payload ) {
            return [];
        }

        return payload.recordings;
    }
    return state;
};

const works = ( state = defaultState.works, { type, payload } ) => {
    if ( type === types.LOAD_WORKS_SUCCESS ) {
        if ( !payload ) {
            return [];
        }

        return payload.works;
    }
    return state;
};

const errors = ( state = defaultState.errors, { type, payload } ) => {
    if ( type === types.LOAD_WORKS ) {
        return {};
    }
    if ( type === types.LOAD_WORKS_ERROR ) {
        return payload;
    }
    return state;
};

export default combineReducers( {
    recordings,
    works,
    errors,
} );
