/* eslint-disable react/prefer-stateless-function */
import React from "react";

import {
    Tab,
} from "semantic-ui-react";
import { Page } from "components";
import PublishersTab from "./PublishersTab";
import LabelsTab from "./LabelsTab";
import MusicCreatorsTab from "./MusicCreatorsTab";
import AddPublisherForm from "../../forms/AddPublisherForm";
import AddMusicCreatorForm from "../../forms/AddMusicCreatorForm";
import AddLabelForm from "../../forms/AddLabelForm";

const panes = setPage => [
    { menuItem: "Publishers", render: () => <Tab.Pane><PublishersTab onAdd={ () => setPage( "add-publisher" ) } /></Tab.Pane> },
    { menuItem: "Labels", render: () => <Tab.Pane><LabelsTab onAdd={ () => setPage( "add-label" ) } /></Tab.Pane> },
    {
        menuItem: "Music creators",
        render: () => (
            <Tab.Pane>
                <MusicCreatorsTab onAdd={ () => setPage( "add-music-creator" ) } />
            </Tab.Pane>
        ),
    },
];

class PagePartners extends React.Component {
    static propTypes = {
    };

    static defaultProps = {
        errors: {},
    };

    constructor( props ) {
        super( props );
        this.state = {
            page: "catalogue",
        };
    }

    setPage=( page ) => {
        this.setState( { page } );
    }

    render() {
        const { page } = this.state;
        return (
            <Page>
                {{
                    "add-publisher": <AddPublisherForm />,
                    "add-music-creator": <AddMusicCreatorForm />,
                    "add-label": <AddLabelForm />,
                    catalogue:
    <Tab panes={ panes( this.setPage ) } id="catalogue-tabs" menu={ { secondary: true, pointing: true } } />,
                }[ page ]}

            </Page>
        );
    }
}

export default PagePartners;
