import {
    call,
    put,
    takeLatest,
    takeEvery,
} from "redux-saga/effects";
import axios from "axios";

import errorSelectors from "components/error/duck/Selectors";
import errorTypes from "components/error/duck/Types";
import types from "./Types";

function* loadRecordings( { payload } ) {
    try {
        const { data } = yield call( axios.get, "/recordings/mine", payload );

        yield put( { type: types.LOAD_RECORDINGS_SUCCESS, payload: { recordings: data } } );
    } catch ( error ) {
        yield put( {
            type: errorSelectors.errorType( error, types.LOAD_RECORDINGS_ERROR, errorTypes.APP_ERROR ),
            payload: errorSelectors.errorPayload( error ),
        } );
    }
}
function* distribute( { payload } ) {
    try {
        const {
            masterfile, image, recordingId, DSPs, date,
        } = payload;
        const formData = new FormData();
        const blob = new Blob( [ JSON.stringify( { recordingId, DSPs, date } ) ], {
            type: "application/json",
        } );
        formData.append( "payload", blob );
        formData.append( "image", image );
        formData.append( "masterfile", masterfile );
        const config = {
            headers: {
                "Content-type": "multipart/form-data",
            },
            onUploadProgress: ( data ) => {
                const progress = Math.round( ( 100 * data.loaded ) / data.total );
                put( { type: types.DISTRIBUTE_PROGRESS, payload: progress } );
            },
        };

        const { data } = yield call( axios.post, "/recordings/master", formData, config );
        yield put( { type: types.DISTRIBUTE_SUCCESS, payload: data } );
    } catch ( error ) {
        yield put( {
            type: errorSelectors.errorType( error, types.DISTRIBUTE_ERROR, errorTypes.APP_ERROR ),
            payload: errorSelectors.errorPayload( error ),
        } );
    }
}
export default function* main() {
    yield takeLatest( types.LOAD_RECORDINGS, loadRecordings );
    yield takeEvery( types.DISTRIBUTE, distribute );
}
