/* eslint-disable react/prefer-stateless-function */
import React from "react";
import { func } from "prop-types";
import {
    Tab,
} from "semantic-ui-react";
import { Page } from "components";
import { connect } from "react-redux";
import { actions } from "./duck";
import RequestsTab from "./RequestsTab";
import StatusTab from "./StatusTab";

const panes = ( {
    workVerificationRequests, recordingVerificationRequests, account, Verify, verifying,
} ) => [
    {
        menuItem: "Requests",
        render: () => (
            <Tab.Pane>
                <RequestsTab
                    workVerificationRequests={ workVerificationRequests }
                    recordingVerificationRequests={ recordingVerificationRequests }
                    account={ account }
                    verify={ Verify }
                    verifying={ verifying }
                />
            </Tab.Pane>
        ),
    },
    {
        menuItem: "Status overview",
        render: () => (
            <Tab.Pane>
                <StatusTab
                    workVerificationRequests={ workVerificationRequests }
                    recordingVerificationRequests={ recordingVerificationRequests }
                    account={ account }
                    verify={ Verify }
                    verifying={ verifying }
                />
            </Tab.Pane>
        ),
    },

];

class PageVerifications extends React.Component {
    componentDidMount() {
        const { Load } = this.props;
        Load();
    }

    render() {
        const {
            workVerificationRequests, recordingVerificationRequests, account, Verify, verifying,
        } = this.props;
        return (
            <Page>

                <Tab
                    panes={ panes( {
                        account, workVerificationRequests, recordingVerificationRequests, Verify, verifying,
                    } ) }
                    className="tabs"
                    menu={ { secondary: true, pointing: true } }
                />
            </Page>
        );
    }
}
PageVerifications.propTypes = {
    Load: func.isRequired,
    Verify: func.isRequired,
};
PageVerifications.defaultProps = {
};

const mSTP = ( {
    app: {
        account,
    },
    verifications: {
        errors,
        workVerificationRequests,
        recordingVerificationRequests,
        verifying,
    },
} ) => ( {
    errors,
    account,
    workVerificationRequests,
    recordingVerificationRequests,
    verifying,
} );

const mDTP = dispatch => ( {
    Load: payload => dispatch( actions.load( payload ) ),
    Verify: payload => dispatch( actions.verify( payload ) ),
} );

export default connect( mSTP, mDTP )( PageVerifications );
