import React from "react";
import {
    Chart,
} from "react-google-charts";

import {
    Loader, Segment, SegmentGroup,
} from "semantic-ui-react";

const calculate = () => {
    const data = [ [ "DSP", "Listeners", "Returning listeners" ] ];
    const DSPs = [ {
        name: "Spotify",
        listeners: 8175000,
        returningListeners: 8008000,
    }, {
        name: "TheSessions",
        listeners: 100034,
        returningListeners: 90234,
    } ];

    DSPs.sort( ( a, b ) => ( b.listeners + b.returningListeners ) - ( a.listeners + a.returningListeners ) );
    DSPs.forEach( ( { name, listeners, returningListeners } ) => data.push( [ name, listeners, returningListeners ] ) );
    return data;
};
const DSPChart = ( ) => (
    <SegmentGroup>
        <Segment>Returning listeners per DSP</Segment>
        <Segment>
            <Chart
                width="500px"
                height="300px"
                chartType="BarChart"
                loader={ <Loader active inline="centered" /> }
                data={ calculate() }
                options={ {
                    title: "",
                    chartArea: { width: "50%" },
                    isStacked: false,
                    animation: {
                        duration: 1000,
                        easing: "out",
                        startup: true,
                    },
                    colors: [ "#4B13A3", "#42d63a" ],

                } }
            />
        </Segment>
    </SegmentGroup>
);

export default DSPChart;
