import React from "react";

import { Page } from "components";
import {
    Tab,
} from "semantic-ui-react";
import {
    arrayOf, func, number, shape, string,
} from "prop-types";
import { connect } from "react-redux";
import DistributeSingleForm from "forms/DistributeSingleForm";
import DistributeAlbumForm from "forms/DistributeAlbumForm";
import SinglesTab from "./SinglesTab";
import AlbumsTab from "./AlbumsTab";
import { actions } from "./duck";

const panes = ( { recordings } ) => [
    { menuItem: "Singles", render: () => <Tab.Pane><SinglesTab recordings={ recordings } /></Tab.Pane> },
    { menuItem: "Albums", render: () => <Tab.Pane><AlbumsTab /></Tab.Pane> },
];
// eslint-disable-next-line react/prefer-stateless-function
class PageDistribution extends React.Component {
    componentDidMount() {
        const { LoadRecordings } = this.props;

        LoadRecordings();
    }

    render() {
        const {
            recordings, match, UploadMasterfile, masterFileProgress, masterFilePath,
        } = this.props;
        const { action = null } = match.params;
        return (
            <Page>
                {{
                    null: <Tab
                        panes={ panes( {
                            setPage: this.setPage,
                            recordings,
                        } ) }
                        className="tabs"
                        menu={ { secondary: true, pointing: true } }
                    />,
                    single: <DistributeSingleForm
                        masterFileProgress={ masterFileProgress }
                        masterFilePath={ masterFilePath }
                        onUploadMasterfile={ UploadMasterfile }
                    />,
                    album: <DistributeAlbumForm />,

                }[ action ]}

            </Page>
        );
    }
}
PageDistribution.propTypes = {
    recordings: arrayOf( shape( {} ) ).isRequired,
    LoadRecordings: func.isRequired,
    UploadMasterfile: func.isRequired,
    masterFileProgress: number.isRequired,
    masterFilePath: string.isRequired,
    match: shape( {} ).isRequired,
};

const mSTP = ( {
    distribution: {
        recordings,
        masterFilePath,
        masterFileProgress,
    },
} ) => ( {
    recordings,
    masterFileProgress,
    masterFilePath,
} );

const mDTP = dispatch => ( {
    LoadRecordings: payload => dispatch( actions.loadRecordings( payload ) ),
    UploadMasterfile: payload => dispatch( actions.uploadMasterfile( payload ) ),
} );

export default connect( mSTP, mDTP )( PageDistribution );
