import React from "react";
import { func, bool } from "prop-types";
import {
    Form, Input, Button, Segment,
} from "semantic-ui-react";
import { connect } from "react-redux";
import actions from "app/duck/Actions";

class LegacyLogin extends React.Component {
    login=() => {
        const { Login } = this.props;
        const { email, password } = this.state;
        Login( { email, password } );
    }

    render() {
        const {
            loggingIn,
        } = this.props;
        return (
            <Segment basic>
                <Form>
                    <Form.Field>
                        <Input
                            placeholder="Email"
                            onChange={ ( e ) => {
                                this.setState( { email: e.currentTarget.value } );
                            } }
                        />
                    </Form.Field>
                    <Form.Field>
                        <Input
                            type="password"
                            placeholder="Password"
                            onChange={ ( e ) => {
                                this.setState( { password: e.currentTarget.value } );
                            } }
                        />
                    </Form.Field>
                    <Form.Field>
                        <Button
                            loading={ loggingIn }
                            type="submit"
                            fluid
                            className="primary"
                            onClick={ this.login }
                        >
Login
                        </Button>
                    </Form.Field>
                </Form>

            </Segment>
        );
    }
}
LegacyLogin.propTypes = {
    loggingIn: bool.isRequired,
    Login: func.isRequired,
};
LegacyLogin.defaultProps = {
};
const mSTP = ( {
    app: {
        isAuthenticated,
        errors,
        loggingIn,
    },
} ) => ( {
    isAuthenticated, errors, loggingIn,
} );

const mDTP = dispatch => ( {
    Login: values => dispatch( actions.login( values ) ),
    Logout: () => dispatch( actions.logout() ),
} );

export default connect( mSTP, mDTP )( LegacyLogin );
