import React from "react";
import PropTypes from "prop-types";
import {
    BrowserRouter,
    Switch,
    Route,
    Redirect,
} from "react-router-dom";

import ROLES from "./Roles";

import {
    PageLogin,
    PageError,
    PageCatalogue,
    PageDashboard,
    PagePartners,
    PageRegister,
    PageRegisterSongs,
    PageSplits,
    PageVerifications,
    PageProfile,
    PageDistribution,
    PageSettings,
} from "../pages";
import RestrictedRoute from "./RestrictedRoute";

const Router = ( {
    isAuthenticated, defaultRoute, roles, children,
} ) => (
    <BrowserRouter>
        <React.Fragment>
            { children }
            <Switch>

                <Route
                    exact
                    path="/"
                    render={ () => ( <Redirect to={ defaultRoute } /> ) }
                />
                <Route exact path="/login" component={ PageLogin } />
                <Route exact path="/register" component={ PageRegister } />
                <RestrictedRoute isAuthenticated={ isAuthenticated } roles={ roles } allowed={ [ ROLES.USER, ROLES.ARTIST, ROLES.AUTHOR, ROLES.LABEL, ROLES.PUBLISHER ] } exact path="/profile" component={ PageProfile } />
                <RestrictedRoute isAuthenticated={ isAuthenticated } roles={ roles } allowed={ [ ROLES.ARTIST, ROLES.AUTHOR, ROLES.LABEL, ROLES.PUBLISHER ] } exact path="/dashboard" component={ PageDashboard } />
                <RestrictedRoute isAuthenticated={ isAuthenticated } roles={ roles } allowed={ [ ROLES.USER, ROLES.ARTIST, ROLES.AUTHOR, ROLES.LABEL, ROLES.PUBLISHER ] } exact path="/catalogue" component={ PageCatalogue } />
                <RestrictedRoute isAuthenticated={ isAuthenticated } roles={ roles } allowed={ [ ROLES.USER, ROLES.ARTIST, ROLES.AUTHOR, ROLES.LABEL, ROLES.PUBLISHER ] } exact path="/partners" component={ PagePartners } />
                <RestrictedRoute isAuthenticated={ isAuthenticated } roles={ roles } allowed={ [ ROLES.ARTIST, ROLES.AUTHOR, ROLES.LABEL, ROLES.PUBLISHER ] } exact path="/registrations" component={ PageRegisterSongs } />
                <RestrictedRoute isAuthenticated={ isAuthenticated } roles={ roles } allowed={ [ ROLES.USER, ROLES.ARTIST, ROLES.AUTHOR, ROLES.LABEL, ROLES.PUBLISHER ] } exact path="/splits" component={ PageSplits } />
                <RestrictedRoute isAuthenticated={ isAuthenticated } roles={ roles } allowed={ [ ROLES.USER, ROLES.ARTIST, ROLES.AUTHOR, ROLES.LABEL, ROLES.PUBLISHER ] } exact path="/approvals" component={ PageVerifications } />
                <RestrictedRoute isAuthenticated={ isAuthenticated } roles={ roles } allowed={ [ ROLES.USER, ROLES.ARTIST, ROLES.LABEL ] } exact path="/distribution/:action?" component={ PageDistribution } />
                <RestrictedRoute isAuthenticated={ isAuthenticated } roles={ roles } allowed={ [ ROLES.USER ] } exact path="/settings" component={ PageSettings } />
                <Route
                    path="*"
                    component={ props => (
                        <PageError
                            code={ 404 }
                            title="Page not found"
                            message={ "Sorry, but the page you are looking for was either not found or does not exist.\nTry refreshing the page or click the button below to go back to the Homepage." }
                            hasBackButton
                            { ...props }
                        />
                    ) }
                />
            </Switch>
        </React.Fragment>
    </BrowserRouter>
);

Router.propTypes = {
    isAuthenticated: PropTypes.bool.isRequired,
    roles: PropTypes.arrayOf( PropTypes.string ).isRequired,
    children: PropTypes.node,
};

Router.defaultProps = {
    children: null,
};

export default Router;
