import {
    call,
    put,
    takeEvery,
} from "redux-saga/effects";
import axios from "axios";

import errorSelectors from "components/error/duck/Selectors";
import errorTypes from "components/error/duck/Types";
import types from "./Types";

function* setAuthorProfile( { payload } ) {
    try {
        const { data } = yield call( axios.post, "/authors", payload );
        yield put( { type: types.SET_AUTHOR_PROFILE_SUCCESS, payload: data } );
    } catch ( error ) {
        yield put( {
            type: errorSelectors.errorType( error, types.SET_AUTHOR_PROFILE_ERROR, errorTypes.APP_ERROR ),
            payload: errorSelectors.errorPayload( error ),
        } );
    }
}

function* setPublisherProfile( { payload } ) {
    try {
        const { data } = yield call( axios.post, "/publishers", payload );
        yield put( { type: types.SET_PUBLISHER_PROFILE_SUCCESS, payload: data } );
    } catch ( error ) {
        yield put( {
            type: errorSelectors.errorType( error, types.SET_PUBLISHER_PROFILE_ERROR, errorTypes.APP_ERROR ),
            payload: errorSelectors.errorPayload( error ),
        } );
    }
}
function* setArtistProfile( { payload } ) {
    try {
        const { data } = yield call( axios.post, "/artists", payload );
        yield put( { type: types.SET_ARTIST_PROFILE_SUCCESS, payload: data } );
    } catch ( error ) {
        yield put( {
            type: errorSelectors.errorType( error, types.SET_ARTIST_PROFILE_ERROR, errorTypes.APP_ERROR ),
            payload: errorSelectors.errorPayload( error ),
        } );
    }
}
function* setLabelProfile( { payload } ) {
    try {
        const { data } = yield call( axios.post, "/labels", payload );
        yield put( { type: types.SET_LABEL_PROFILE_SUCCESS, payload: data } );
    } catch ( error ) {
        yield put( {
            type: errorSelectors.errorType( error, types.SET_LABEL_PROFILE_ERROR, errorTypes.APP_ERROR ),
            payload: errorSelectors.errorPayload( error ),
        } );
    }
}

function* load( { payload } ) {
    try {
        const { data } = yield call( axios.get, "/accounts/me", payload );
        yield put( { type: types.LOAD_SUCCESS, payload: data } );
    } catch ( error ) {
        yield put( {
            type: errorSelectors.errorType( error, types.LOAD_ERROR, errorTypes.APP_ERROR ),
            payload: errorSelectors.errorPayload( error ),
        } );
    }
}

export default function* main() {
    yield takeEvery( types.SET_AUTHOR_PROFILE, setAuthorProfile );
    yield takeEvery( types.SET_PUBLISHER_PROFILE, setPublisherProfile );
    yield takeEvery( types.SET_ARTIST_PROFILE, setArtistProfile );
    yield takeEvery( types.SET_LABEL_PROFILE, setLabelProfile );
    yield takeEvery( types.LOAD, load );
}
