/* eslint-disable no-continue */
/* eslint-disable complexity */
/* eslint-disable no-useless-escape */
import React from "react";
import {
    Search, Button, Icon, Feed, Segment, SegmentGroup, Radio, Form, Header, Input, Select,
} from "semantic-ui-react";
import { Chart } from "react-google-charts";
import MultiSlider from "multi-slider";
import classnames from "classnames";
import {
    shape, func, arrayOf, bool,
} from "prop-types";

import countryList from "iso-3166-country-list";

const countries = countryList.map( country => ( { key: country.code, text: country.name, value: country.code } ) );
const palette = [ "#eb4034", "#ffbb00", "#a6ff00", "#00ffd0", "#009dff", "#002cf2", "#6a00ff", "#ea00ff" ];
const defaultState = {
    splitType: "equal",
    splits: new Map(),
    roles: [],
    additionalArtists: [ {
        name: null,
        id: null,
    } ],
    labels: [ {
        name: null,
        id: null,
    } ],
    title: null,
    alternateTitles: [],
    versionTitle: null,
    duration: null,
    released: null,
    creditedArtist: null,
    isrc: null,
    territories: countries.map( c => c.value ),
    createdIn: [],
    unverifiedOriginatingISRCs: [],
    unverifiedOriginatingISWCs: [],
    unverifiedDerivativeISRCs: [],
    selectCountries: false,
    ownerSearchQuery: null,

};
class RegisterRecordingForm extends React.Component {
    constructor( props ) {
        super( props );
        this.state = defaultState;
    }

    handleChangeSplitType = ( e, { value } ) => {
        const { additionalArtists, labels } = this.state;
        const owners = [ ...additionalArtists, ...labels ];
        if ( value === "equal" ) {
            const newSplits = new Map();
            owners.forEach( ( owner ) => {
                if ( owner.name && owner.id ) {
                    newSplits.set( owner.id, 100 / owners.length );
                }
            } );
            this.setState( { splits: newSplits } );
        }
        this.setState( { splitType: value } );
    }

    register = () => {
        const { onRegister } = this.props;
        const {
            title,
            labels,
            additionalArtists,
            splits,
            alternateTitles,
            versionTitle,
            duration,
            released,
            edited,
            isrc,
            roles,
            territories,
            createdIn,
            creditedArtist,
        } = this.state;

        // roles convert index to id or email
        const resolvedRoles = new Map();
        for ( let i = 0; i < roles.length; i += 1 ) {
            const value = roles[ i ];
            if ( !value ) {
                continue;
            }
            const idOrEmail = additionalArtists[ i ].id;
            resolvedRoles.set( idOrEmail, value );
        }

        onRegister( {
            title,
            labels: labels.filter( a => a.id != null ).map( a => a.id ),
            additionalArtists: additionalArtists.filter( a => a.id != null ).map( a => a.id ),
            splits,
            creditedArtist,
            alternateTitles,
            versionTitle,
            duration,
            released,
            isrc,
            edited,
            roles: resolvedRoles,
            territories,
            createdIn,
        } );
    }

    addArtist = () => {
        const { additionalArtists } = this.state;

        this.setState( {
            additionalArtists:
            [ ...additionalArtists, { name: null, id: null } ],
        } );
    }

    handleSearchAccount=( e, data, type ) => {
        const query = data.value;
        const { searchAccounts } = this.props;
        searchAccounts( { type, query } );
        this.setState( { ownerSearchQuery: query } );
    }

    setOwner=( i, data, type ) => {
        const {
            additionalArtists, labels, splits, splitType,
        } = this.state;

        const newSplits = new Map();

        if ( splitType === "equal" ) {
            const validOwners = [ ...labels, ...additionalArtists ].filter( a => a.id !== null );
            validOwners.forEach( ( owner ) => {
                newSplits.set( owner.id, 100 / ( validOwners.length + 1 ) );
            } );
            newSplits.set( data.id, 100 / ( validOwners.length + 1 ) );
        } else {
            splits.set( data.id, 0 );
        }

        if ( type === "label" ) {
            this.setState( {
                labels: labels.map( ( a, index ) => {
                    if ( i === index ) {
                        return data;
                    }
                    return a;
                } ),
                splits: splitType === "equal" ? newSplits : splits,
            } );
        } else if ( type === "artist" ) {
            this.setState( {
                additionalArtists: additionalArtists.map( ( a, index ) => {
                    if ( i === index ) {
                        return data;
                    }
                    return a;
                } ),
                splits: splitType === "equal" ? newSplits : splits,
            } );
        }
    }

    removeLabel = ( index ) => {
        const { labels, splits } = this.state;

        const label = labels[ index ];
        splits.set( label.id, 0 );

        this.setState( {
            labels: labels.filter( ( a, i ) => i !== index ),
            splits,
        } );
    }

    removeArtist = ( index ) => {
        const { additionalArtists, splits } = this.state;
        const artist = additionalArtists[ index ];
        splits.set( artist.id, 0 );
        this.setState( {
            additionalArtists: additionalArtists.filter( ( a, i ) => i !== index ),
            splits,
        } );
    }

    setRole=( index, role ) => {
        const { roles } = this.state;
        roles[ index ] = role;
        this.setState( { roles } );
    }

    renderOwnerSearchResult=( { title, value } ) => {
        const invite = title.indexOf( "Invite:" ) !== -1;
        return (
            <Feed>
                <Feed.Event>
                    <Feed.Label>

                        {invite
                            ? <Icon name="mail outline" />
                            : <img alt="owner" src={ value.artist.image || value.label.image || "https://lh3.googleusercontent.com/proxy/2ec2fsuomg4sFdIIEdMqaK0yB1c_-D-YFhGBrxd1Vzg9EbdKkdgw_34aE4-TOIBFk5283SbZLnK6hCUGkY1dpdoR8XV4DmaIz6cSF76FgF2cpe2gxdC7HDHJzVKCm_P6rZHbfUsRCQ" } />}
                    </Feed.Label>
                    <Feed.Content>
                        <Feed.Summary>
                            {title}

                        </Feed.Summary>
                    </Feed.Content>
                </Feed.Event>
            </Feed>
        );
    }

    isFormValid = () => {
        const {
            createdIn, territories, title, splits, roles, labels, additionalArtists, creditedArtist, isrc,
        } = this.state;
        const { account } = this.props;

        if ( !isrc || !createdIn || !title || !territories || !splits || !roles || !creditedArtist ) {
            return false;
        }
        if ( createdIn.length <= 0 || territories.length <= 0 ) {
            return false;
        }

        for ( let i = 0; i < additionalArtists.length; i += 1 ) {
            if ( !roles[ i ] ) {
                return false;
            }
            if ( !splits.get( additionalArtists[ i ].id ) ) {
                return false;
            }
        }
        for ( let j = 0; j < labels.length; j += 1 ) {
            if ( !splits.get( labels[ j ].id ) ) {
                return false;
            }
        }

        if ( !additionalArtists.find( author => author.id === account.id ) && !labels.find( publisher => publisher.id === account.id ) ) {
            return false;
        }
        return true;
    }

    render() {
        const {
            ownerSearchQuery, isrc, territories, labels, selectCountries, additionalArtists, splits, splitType, alternateTitles, unverifiedOriginatingISRCs, unverifiedDerivativeISRCs, unverifiedOriginatingISWCs,
        } = this.state;
        const {
            artists: artistSearchResults, labels: labelSearchResults, registerRecordingProcessing,
        } = this.props;
        const slices = {
        };

        const owners = [ ...additionalArtists, ...labels ];
        owners.forEach( ( a, i ) => {
            slices[ i ] = { color: palette[ i ] };
        } );

        const pieSplits = owners.filter( owner => owner.id !== null ).map( owner => [ owner.artist ? owner.artist.name : owner.label.name, splits.get( owner.id ) ] );
        pieSplits.unshift(
            [ "Partner", "Split percentage" ],
        );
        return (

            <div>
                <SegmentGroup>
                    <Segment>
                        <Header as="h4">Main information</Header>
                    </Segment>
                    <Segment className="form-body">
                        <Form>
                            <Form.Group widths="equal">
                                <Form.Input
                                    placeholder="Title of the recording"
                                    required
                                    label="Title"
                                    onChange={ e => this.setState( { title: e.target.value } ) }
                                />

                                <Form.Input
                                    label="Version title"
                                    placeholder="Version title"
                                    onChange={ e => this.setState( { versionTitle: e.target.value } ) }
                                />
                                <Form.Dropdown

                                    label="Alternate titles"
                                    placeholder="Alternate titles"
                                    fluid
                                    search
                                    selection
                                    multiple
                                    allowAdditions
                                    onAddItem={ ( event, { value } ) => this.setState( { alternateTitles: [ ...alternateTitles, { key: value, text: value, value } ] } ) }
                                    options={ alternateTitles }
                                />
                            </Form.Group>
                            <Form.Group widths="equal">
                                <Form.Input
                                    label="Release date"
                                    placeholder="DD/MM/YYYY"
                                    onChange={ e => this.setState( { released: e.target.value } ) }
                                />
                                <Form.Input
                                    placeholder="hh:mm:ss"
                                    label="Duration"
                                    onChange={ e => this.setState( { duration: e.target.value } ) }
                                />

                            </Form.Group>

                            <Form.Group widths="equal">
                                <Form.Input
                                    placeholder="Album name / EAN / UPC"
                                    disabled
                                    label="Album"
                                />

                            </Form.Group>
                        </Form>
                    </Segment>
                </SegmentGroup>
                <SegmentGroup>
                    <Segment>
                        <Header as="h4">Identifiers</Header>
                    </Segment>
                    <Segment className="form-body">
                        <Form>
                            <Form.Group widths="equal">

                                <Form.Input
                                    label="ISRC"
                                    required
                                >
                                    <Input
                                        placeholder="ISRC"
                                        style={ { width: "100%" } }
                                        value={ isrc }
                                        onChange={ e => this.setState( { isrc: e.target.value } ) }
                                    />
                                    <Button
                                        size="tiny"
                                        onClick={ () => {
                                            const isrcStub = "NL-4BP-21-";
                                            this.setState( { isrc: `${ isrcStub }00001` } );
                                        } }
                                    >
                                               Create

                                    </Button>
                                </Form.Input>

                                <Form.Dropdown
                                    label="Originating ISWCs"
                                    placeholder="Originating ISWCs"
                                    fluid
                                    search
                                    selection
                                    multiple
                                    allowAdditions
                                    onAddItem={ ( event, data ) => {
                                        const iswc = data.value;
                                        if ( "[\\p{Alnum}\\-\\s]{10,13}".match( iswc ) ) {
                                            this.setState( { unverifiedOriginatingISWCs: [ ...unverifiedOriginatingISWCs, { key: iswc, text: iswc, value: iswc } ] } );
                                        }
                                    } }
                                    options={ unverifiedOriginatingISRCs }
                                />
                            </Form.Group>
                            <Form.Group widths="equal">

                                <Form.Dropdown
                                    label="Originating ISRCs"
                                    placeholder="Originating ISRCs"
                                    fluid
                                    search
                                    selection
                                    multiple
                                    allowAdditions
                                    onAddItem={ ( event, data ) => {
                                        const isrc = data.value;
                                        if ( "[A-Z]{2}-[A-Z\d]{3}-\d{2}-\d{5}".match( isrc ) ) {
                                            this.setState( { unverifiedOriginatingISRCs: [ ...unverifiedOriginatingISRCs, { key: isrc, text: isrc, value: isrc } ] } );
                                        }
                                    } }
                                    options={ unverifiedOriginatingISRCs }
                                />
                                <Form.Dropdown
                                    label="Derivative ISRCs"
                                    placeholder="Derivative ISRCs"
                                    fluid
                                    search
                                    selection
                                    multiple
                                    allowAdditions
                                    onAddItem={ ( event, data ) => {
                                        const isrc = data.value;
                                        if ( "[A-Z]{2}-[A-Z\d]{3}-\d{2}-\d{5}".match( isrc ) ) {
                                            this.setState( { unverifiedDerivativeISRCs: [ ...unverifiedDerivativeISRCs, { key: isrc, text: isrc, value: isrc } ] } );
                                        }
                                    } }
                                    options={ unverifiedDerivativeISRCs }
                                />
                            </Form.Group>
                        </Form>
                    </Segment>
                </SegmentGroup>
                <SegmentGroup>
                    <Segment>
                        <Header as="h4">Credited Artist</Header>
                    </Segment>
                    <Segment className="form-body">
                        <Form>
                            <Form.Group widths="equal">
                                <Form.Input
                                    placeholder="Credited artist"
                                    required
                                    label="Credited artist"
                                    onChange={ e => this.setState( { creditedArtist: e.target.value } ) }
                                />

                            </Form.Group>

                        </Form>
                    </Segment>
                </SegmentGroup>
                <SegmentGroup>
                    <Segment>
                        <Header as="h4">Recording Artists</Header>
                    </Segment>
                    <Segment className="form-body">
                        <Form>
                            {additionalArtists.map( ( account, i ) => (
                                <Form.Group widths="equal">
                                    <Form.Input
                                        required
                                        label="Name or Email"
                                    >
                                        <Search
                                            style={ { width: "100%" } }
                                            placeholder="Name or Email"
                                            selectFirstResult
                                            value={ account.artist ? account.artist.name : account.id }
                                            onResultSelect={ ( e, data ) => this.setOwner( i, data.result.value, "artist" ) }
                                            onSearchChange={ ( e, data ) => this.handleSearchAccount( e, data, "artists" ) }
                                            resultRenderer={ this.renderOwnerSearchResult }
                                            results={ artistSearchResults.length > 0
                                                ? artistSearchResults.map( a => ( { title: `${ a.artist.name }`, value: a } ) )
                                                : [ { title: `Invite: ${ ownerSearchQuery }`, value: { id: ownerSearchQuery } } ] }
                                        />
                                    </Form.Input>

                                    <Form.Input label="ISNI " placeholder="ISNI" value={ account.artist && account.artist.isni } />
                                    <Form.Input label="IPI " placeholder="IPI" value={ account.artist && account.artist.ipi } />

                                    <Form.Input label="Country" placeholder="Country" value={ account.artist && account.artist.country } />
                                    <Form.Input
                                        label="Role"
                                        required
                                    >
                                        <Select
                                            onChange={ ( e, { value } ) => this.setRole( i, value ) }
                                            fluid
                                            options={ [
                                                { key: "FEATURING", text: "Featuring Artist", value: "FEATURING" },
                                                { key: "GUEST", text: "Guest Artist", value: "GUEST" },
                                            ] }
                                            placeholder="Select the artist's role"
                                            style={ { width: "100%", marginRight: "10px" } }
                                        />
                                        <Button
                                            color="red"
                                            icon
                                            size="tiny"
                                            onClick={ () => this.removeArtist( i ) }
                                        >
                                            <Icon name="delete" />

                                        </Button>

                                    </Form.Input>
                                </Form.Group>
                            ) )}

                            <Button
                                className="primary"
                                icon
                                onClick={ this.addArtist }
                            >
                                <Icon name="plus" />

                            </Button>
                        </Form>

                    </Segment>
                </SegmentGroup>
                <SegmentGroup>
                    <Segment>
                        <Header as="h4">Label</Header>
                    </Segment>
                    <Segment className="form-body">
                        <Form>
                            {labels.map( ( label, i ) => (
                                <Form.Group key={ label.id } widths="equal">
                                    <Form.Input
                                        required
                                        label="Name"
                                    >
                                        <Search
                                            style={ { width: "100%" } }
                                            placeholder="Name or Email"
                                            resultRenderer={ this.renderOwnerSearchResult }
                                            selectFirstResult
                                            onResultSelect={ ( e, data ) => this.setOwner( i, data.result.value, "lavel" ) }
                                            onSearchChange={ ( e, data ) => this.handleSearchAccount( e, data, "labels" ) }
                                            results={ labelSearchResults.length > 0
                                                ? labelSearchResults.map( a => ( { title: `${ a.label.name }`, value: a } ) )
                                                : [ { title: `Invite: ${ ownerSearchQuery }`, value: { id: ownerSearchQuery } } ] }
                                        />

                                    </Form.Input>
                                    {label.country && (
                                        <Form.Input
                                            label="Country"
                                            placeholder="Country"
                                            value={ label.country }
                                        />
                                    )}

                                    <Form.Input
                                        label="ISNI"
                                        placeholder="ISNI"
                                        value={ label.isni }
                                    />

                                    <Form.Input
                                        label="IPI"
                                        placeholder="IPI"
                                        value={ label.ipi }
                                    />
                                    <Form.Input
                                        label="Code"
                                    >
                                        <Input
                                            style={ { width: "100%", marginRight: "10px" } }
                                            value={ label.code }
                                            placeholder="Label Code"
                                        />
                                        <Button
                                            color="red"
                                            icon
                                            size="tiny"
                                            onClick={ () => this.removeLabel( i ) }
                                        >
                                            <Icon name="delete" />

                                        </Button>

                                    </Form.Input>

                                </Form.Group>
                            ) )}

                            <Button
                                className="primary"
                                icon
                                onClick={ this.addLabel }
                            >
                                <Icon name="plus" />

                            </Button>
                        </Form>
                    </Segment>
                </SegmentGroup>
                <SegmentGroup>
                    <Segment>
                        <Header as="h4">Splits</Header>
                    </Segment>
                    <Segment>

                        <Form>
                            <Form.Group>
                                <Form.Field>
                                    <Radio
                                        label="Equal split"
                                        value="equal"
                                        checked={ splitType === "equal" }
                                        onChange={ this.handleChangeSplitType }
                                    />
                                </Form.Field>

                                <Form.Field>
                                    <Radio
                                        label="Custom split"
                                        value="custom"
                                        checked={ splitType === "custom" }
                                        onChange={ this.handleChangeSplitType }
                                    />
                                </Form.Field>
                            </Form.Group>
                            <Form.Group>
                                <Form.Field>
                                    <MultiSlider
                                        onChange={ ( values ) => {
                                            if ( splitType === "custom" ) {
                                                values.forEach( ( split, i ) => {
                                                    const id = Array.from( splits.keys() )[ i ];
                                                    splits.set( id, split );
                                                } );
                                                this.setState( { splits } );
                                            }
                                        } }
                                        width={ 400 }
                                        colors={ palette.slice( 0, splits.size ) }
                                        values={ Array.from( splits.values() ) }
                                        trackSize={ 20 }
                                    />

                                </Form.Field>
                                <Form.Field>
                                    <Chart
                                        width="500px"
                                        height="300px"
                                        chartType="PieChart"
                                        loader={ <div>Loading</div> }
                                        data={
                                            pieSplits
                                        }
                                        options={ {
                                            title: "",
                                            slices,
                                        } }
                                        rootProps={ { "data-testid": "1" } }
                                    />
                                </Form.Field>
                            </Form.Group>
                        </Form>
                    </Segment>
                </SegmentGroup>
                <SegmentGroup>
                    <Segment>
                        <Header as="h4">Territories</Header>
                    </Segment>
                    <Segment className="form-body">
                        <Form>
                            <Segment>
                                <Form.Group>
                                    <Form.Dropdown
                                        label="Created in"
                                        required
                                        multiple
                                        placeholder="Select one or more"
                                        onChange={ ( e, data ) => {
                                            this.setState( { createdIn: data.value } );
                                        } }
                                        selection
                                        options={ countries }
                                    />
                                </Form.Group>

                            </Segment>
                            <Segment>
                                <Form.Group widths="equal">
                                    <Form.Input
                                        required
                                        label="Represent this musical work in"
                                    >
                                        <div style={ { display: "block", width: "100%" } }>
                                            <div className="select-all-checkbox">
                                                <Form.Field>
                                                    <Radio
                                                        label="Worldwide"
                                                        name="radioGroup"
                                                        defaultChecked
                                                        checked={ !selectCountries }
                                                        onChange={ ( e, data ) => {
                                                            if ( data.checked ) {
                                                                this.setState( { selectCountries: false, territories: countries.map( c => c.value ) } );
                                                            } else {
                                                                this.setState( { selectCountries: true, territories: [] } );
                                                            }
                                                        } }
                                                    />
                                                </Form.Field>
                                                <Form.Field>
                                                    <Radio
                                                        label="Select countries"
                                                        name="radioGroup"
                                                        checked={ selectCountries }
                                                        onChange={ ( e, data ) => {
                                                            if ( data.checked ) {
                                                                this.setState( { selectCountries: true, territories: [] } );
                                                            }
                                                        } }
                                                    />
                                                </Form.Field>
                                            </div>
                                            <div className="country-checkboxes">
                                                {selectCountries && countries.map( c => (
                                                    <Form.Checkbox
                                                        checked={ territories.includes( c.value ) }
                                                        label={ c.text }
                                                        onChange={ ( e, data ) => {
                                                            if ( data.checked ) {
                                                                this.setState( { territories: [ ...territories, c.value ] } );
                                                            } else {
                                                                this.setState( { territories: territories.filter( t => t !== c.value ) } );
                                                            }
                                                        } }
                                                    >
                                                    </Form.Checkbox>
                                                ) )}
                                            </div>

                                        </div>

                                    </Form.Input>

                                </Form.Group>

                            </Segment>
                        </Form>
                    </Segment>
                </SegmentGroup>
                <Button
                    className={ classnames( this.isFormValid() ? "primary" : "" ) }
                    icon
                    disabled={ !this.isFormValid() }
                    labelPosition="right"
                    loading={ registerRecordingProcessing }
                    onClick={ this.register }
                >
      Confirm
                    <Icon name="right arrow" />
                </Button>
            </div>
        );
    }
}
RegisterRecordingForm.propTypes = {
    onRegister: func.isRequired,
    searchAccounts: func.isRequired,
    registerRecordingProcessing: bool.isRequired,
    account: shape( {} ).isRequired,
    artists: arrayOf(),
    labels: arrayOf(),
};
RegisterRecordingForm.defaultProps = {
    artists: [],
    labels: [],
};
export default RegisterRecordingForm;
