/* eslint-disable complexity */
import React from "react";
import {
    Button, Icon, Segment, SegmentGroup, Form, Image, Header,
} from "semantic-ui-react";
import { Link } from "react-router-dom";
import classnames from "classnames";
import {
    arrayOf, bool, func, number, shape, string,
} from "prop-types";
import DatetimePicker from "react-semantic-datetime";
import moment from "moment";

const DSPOptions = [
    { key: "spotify", text: "Spotify", value: "spotify" },

];
class DistributeSingleForm extends React.Component {
    constructor( props ) {
        super( props );
        this.state = {
            selectedRecording: null,
            masterRecordingFile: null,
            coverArtFile: null,
            coverArtFilePreview: null,
        };
        this.masterRecordingRef = React.createRef();
        this.coverArtRef = React.createRef();
    }

    isFormValid=() => true

    masterRecordingFileChange = ( e ) => {
        this.setState( { masterRecordingFile: e.target.files[ 0 ] } );
    };

    coverArtFileChange = ( e ) => {
        this.setState( {
            coverArtFile: e.target.files[ 0 ],
            coverArtFilePreview: URL.createObjectURL( e.target.files[ 0 ] ),
        } );
    };

    render() {
        const {
            processingDistribution, recordings, onUploadMasterfile, masterFileProgress, masterFilePath,
        } = this.props;
        const {
            selectedRecording, masterRecordingFile, coverArtFilePreview, date, coverArtFile,
        } = this.state;
        return (
            <div>
                <div className="page-header">
                    <Link to="/distribution">
                        <Icon name="arrow left" />
                        <h1> Single</h1>
                    </Link>
                </div>

                <div className="page-content">
                    <div className="ui middle aligned center aligned grid">
                        <div>
                            <Image src={ coverArtFilePreview || "https://react.semantic-ui.com/images/wireframe/image.png" } size="medium" />

                            min. 640x640 pixels

                            <Form>
                                <Form.Group>
                                    <Form.Button
                                        content="Choose File"
                                        labelPosition="left"
                                        icon="file"
                                        onClick={ () => this.coverArtRef.current.click() }
                                    />
                                    <input
                                        ref={ this.coverArtRef }
                                        type="file"
                                        accept=".png,.jpg"
                                        hidden
                                        onChange={ this.coverArtFileChange }
                                    />

                                </Form.Group>

                            </Form>
                        </div>
                    </div>
                    <SegmentGroup>
                        <Segment>
                            <Header as="h4">Master recording</Header>
                        </Segment>
                        <Segment>
                            {masterFilePath && (
                                <span>
Uploaded:
                                    {" "}
                                    {masterFilePath}
                                </span>
                            )}
                            <Form>
                                <Form.Group>
                                    <Form.Button
                                        content="Choose File"
                                        labelPosition="left"
                                        icon="file"
                                        onClick={ () => this.masterRecordingRef.current.click() }
                                    />
                                    <input
                                        ref={ this.masterRecordingRef }
                                        type="file"
                                        accept=".wav,.flac"
                                        hidden
                                        onChange={ this.masterRecordingFileChange }
                                    />

                                </Form.Group>

                            </Form>
                        </Segment>
                    </SegmentGroup>

                    <SegmentGroup>
                        <Segment>
                            <Header as="h4">Distribution</Header>
                        </Segment>
                        <Segment className="form-body">
                            <Form>
                                <Form.Group>

                                    <Form.Input
                                        label="Global Release Date"
                                    >
                                        <DatetimePicker
                                            color="#4B13A3" // optional (default:teal)
                                            onChange={ ( value ) => { this.setState( { date: value } ); } }
                                            value={ moment( date ) }
                                            time
                                        />
                                    </Form.Input>
                                    <Form.Dropdown
                                        width={ 4 }
                                        label="Distribute to"
                                        placeholder="DSPs"
                                        fluid
                                        multiple
                                        selection
                                        options={ DSPOptions }
                                    />
                                    <Form.Dropdown
                                        placeholder="Select Recording"
                                        fluid
                                        selection
                                        label="Recording"
                                        width={ 4 }
                                        options={ recordings.map( r => ( { key: r.id, text: r.title, value: r } ) ) }
                                    />
                                </Form.Group>
                                <Form.Group widths="equal">

                                </Form.Group>

                            </Form>
                        </Segment>
                    </SegmentGroup>
                    <Button
                        className={ classnames( this.isFormValid() ? "primary" : "" ) }
                        icon
                        disabled={ !this.isFormValid() }
                        labelPosition="right"
                        loading={ processingDistribution }
                        onClick={ this.register }
                    >
          Confirm
                        <Icon name="right arrow" />
                    </Button>
                </div>
            </div>
        );
    }
}
DistributeSingleForm.defaultProps = {
    recordings: [],
    processingDistribution: false,
    onUploadMasterfile: () => {},
    masterFileProgress: 0,
    masterFilePath: null,

};
DistributeSingleForm.propTypes = {
    recordings: arrayOf( shape( {} ) ),
    processingDistribution: bool,
    onUploadMasterfile: func,
    masterFileProgress: number,
    masterFilePath: string,
};
export default DistributeSingleForm;
