import React from "react";
import { arrayOf, shape } from "prop-types";
import {
    Table, Header, Menu, Icon, Loader,
} from "semantic-ui-react";

import {
    Chart,
} from "react-google-charts";

const calculate = ( recording ) => {
    const data = [ [ "Owner", "Percentage" ] ];
    const owners = [];

    recording.splits.forEach( ( entry ) => {
        const percentage = parseFloat( entry.percentage.toFixed( 2 ) );
        const owner = entry.account;
        let name = "";
        if ( recording.additionalArtists.find( artist => artist.id === owner.id ) ) {
            name = `${ owner.artist.name }`;
        } else if ( recording.labels.find( label => label.id === owner.id ) ) {
            name = `${ owner.label.name }`;
        }
        owners.push( { name, percentage } );
    } );
    owners.sort( ( a, b ) => b.percentage - a.percentage );
    owners.forEach( ( { name, percentage } ) => data.push( [ name, percentage ] ) );
    return data;
};

const RecordingsTab = ( { recordings } ) => (

    <div className="page-content">
        <Table basic>
            <Table.Header>
                <Table.Row>
                    <Table.HeaderCell>
Title
                    </Table.HeaderCell>
                    <Table.HeaderCell>
Holders
                    </Table.HeaderCell>

                </Table.Row>
            </Table.Header>

            <Table.Body>
                {recordings.map( w => (
                    <Table.Row key={ w.id }>

                        <Table.Cell verticalAlign="top">
                            <Header as="h5">{w.title}</Header>
                        </Table.Cell>
                        <Table.Cell>
                            <Chart
                                chartType="BarChart"
                                loader={ <Loader active inline="centered" /> }
                                data={ calculate( w ) }

                                options={ {
                                    bar: {
                                        groupWidth: "75%",
                                    },
                                    legend: { position: "none" },
                                    hAxis: {
                                        minValue: 0,
                                        maxValue: 100,
                                    },
                                    chartArea: {
                                        height: "100%",
                                        width: "100%",
                                        top: 0,
                                        left: 140,
                                        right: 48,
                                        bottom: 48,
                                    },
                                    height: `${ ( 1 + w.additionalArtists.length + w.labels.length ) * 20 + 60 }px`,
                                    width: "1200px",
                                    isStacked: false,
                                    animation: {
                                        duration: 500,
                                        easing: "out",
                                        startup: true,
                                    },
                                    colors: [ "#4B13A3", "#42d63a" ],
                                } }
                            />
                        </Table.Cell>

                    </Table.Row>

                ) )}
            </Table.Body>

            <Table.Footer>
                <Table.Row>
                    <Table.HeaderCell colSpan="10">
                        <Menu floated="left" pagination id="table-footer">
                            <Menu.Item as="a" icon>
                                <Icon name="chevron left" />
                            </Menu.Item>
                            <Menu.Item as="a"><span>1</span></Menu.Item>

                            <Menu.Item as="a" icon>
                                <Icon name="chevron right" />
                            </Menu.Item>
                        </Menu>
                        <Menu floated="right" id="table-footer-right">

                            <Menu.Item as="a"><span>10</span></Menu.Item>
                            <Menu.Item as="a" disabled><span>/</span></Menu.Item>
                            <Menu.Item as="a"><span>50</span></Menu.Item>
                            <Menu.Item as="a" disabled><span>/</span></Menu.Item>
                            <Menu.Item as="a"><span>100</span></Menu.Item>

                        </Menu>
                    </Table.HeaderCell>
                </Table.Row>
            </Table.Footer>

        </Table>
    </div>
);
RecordingsTab.propTypes = {
    recordings: arrayOf( shape( {} ) ).isRequired,
};
export default RecordingsTab;
