/* eslint-disable complexity */
import React, { Component } from "react";
import {
    Segment, Icon, Grid, Header,
} from "semantic-ui-react";
import {
    shape, bool,
} from "prop-types";
import moment from "moment";

class Verifier extends Component {
    getImageUrl=() => {
        const {
            verifier,
        } = this.props;
        const placeholder = "https://autototaalbv.nl/wp-content/uploads/2017/04/profile-placeholder.png";
        if ( verifier.artist ) {
            return verifier.artist.image || placeholder;
        }
        if ( verifier.author ) {
            return verifier.author.image || placeholder;
        }
        if ( verifier.publisher ) {
            return verifier.publisher.image || placeholder;
        }
        if ( verifier.label ) {
            return verifier.label.image || placeholder;
        }
        return placeholder;
    }

    getName=() => {
        const {
            verifier,
        } = this.props;
        if ( verifier.artist ) {
            return verifier.artist.name;
        }
        if ( verifier.author ) {
            return `${ verifier.firstname } ${ verifier.lastname }`;
        }
        if ( verifier.publisher ) {
            return verifier.publisher.name;
        }
        if ( verifier.label ) {
            return verifier.label.name;
        }
        return verifier.email;
    }

    capitalize = ( s ) => {
        if ( typeof s !== "string" ) return "";
        return s.charAt( 0 ).toUpperCase() + s.slice( 1 ).toLowerCase();
    }

    getRole=() => {
        const {
            verifier, request,
        } = this.props;
        if ( request.authors && request.authors.find( account => account.id === verifier.id ) ) {
            const { roles } = request.roles.find( entry => entry.account.id === verifier.id );
            if ( roles.length > 1 ) {
                return `${ this.capitalize( roles[ 0 ] ) } & ${ this.capitalize( roles[ 1 ] ) }`;
            }
            return this.capitalize( roles[ 0 ] );
        }
        if ( request.additionalArtists && request.additionalArtists.find( account => account.id === verifier.id ) ) {
            const { role } = request.roles.find( entry => entry.account.id === verifier.id );
            return this.capitalize( role );
        }
        return "";
    }

    hasVerified = () => {
        const { verifier, request } = this.props;
        return request.verifiedBy.find( entry => entry.account.id === verifier.id );
    }

    render() {
        const {
            verifier, date,
        } = this.props;
        return (
            <div
                key={ verifier.id }
                className="verification-verifier"
                id={ this.hasVerified() && "verified" }
            >
                <Icon
                    name={ date ? "check circle outline" : "times circle outline" }
                    size="big"
                    style={ {
                        color: date ? "#41CCAA" : "#F6318B",
                        flex: 0,
                        alignSelf: "center",
                        marginRight: "15px",
                        marginLeft: "8px",
                    } }
                />
                <div className="date">
                    {date

                        ? (
                            <>
                                <span className="day">
                                    {moment( date ).format( "dddd DD" )}
                                </span>
                                <span className="time">
                                    {moment( date ).format( "HH:mm" )}
                                </span>
                            </>
                        ) : "-"}
                </div>
                <div className="verifier">
                    <img
                        className="picture"
                        alt="owner"
                        src={ this.getImageUrl() }
                    />
                    <div className="title">
                        <span className="name">{this.getName()}</span>
                        <span className="role">{this.getRole()}</span>
                    </div>

                </div>
                <span className="description">
                    {"Sed ut perspiciatis, unde omnis iste natus error sit voluptatem accusantium doloremque laudantium"}
                </span>

            </div>
        );
    }
}
Verifier.defaultProps = {
};
Verifier.propTypes = {
    verifier: shape( {} ).isRequired,
};
export default Verifier;
