import React from "react";
import {
    Header, Button, SegmentGroup, Segment, Form,
} from "semantic-ui-react";
import { CSVReader } from "react-papaparse";

const UploadBatch = ( {
    title, onUpload, onError, onRemove,
} ) => (
    <SegmentGroup>
        <Segment>
            <Header as="h4">{title}</Header>
        </Segment>
        <Segment>
            <Form>
                <Form.Group>
                    <CSVReader
                        onDrop={ onUpload }
                        onError={ onError }
                        addRemoveButton
                        removeButtonColor="#dc3545"
                        progressBarColor="#4B13A3"
                        onRemoveFile={ onRemove }
                        config={ { delimiter: ";" } }
                    >
                        <span>Drop CSV file here or click to upload.</span>
                    </CSVReader>

                </Form.Group>
            </Form>
            <Button
                content="Download template"
                labelPosition="left"
                icon="file"
                onClick={ () => {} }
            />
        </Segment>
    </SegmentGroup>
);
export default UploadBatch;
