import React from "react";
import { Redirect } from "react-router-dom";
import { connect } from "react-redux";
import queryString from "query-string";
import { func, shape, bool } from "prop-types";
import actions from "app/duck/Actions";
import {
    Segment, Tab,
} from "semantic-ui-react";
import LegacyLogin from "./LegacyLogin";
import NextGenerationLogin from "./NextGenerationLogin";

const panes = () => [ {
    menuItem: "Current",
    render: () => (
        <Tab.Pane style={ { border: 0 } }>
            <LegacyLogin />
        </Tab.Pane>
    ),
},
{
    menuItem: "Next",
    render: () => (
        <Tab.Pane style={ { border: 0 } }>
            <NextGenerationLogin />
        </Tab.Pane>
    ),
} ];

class PageLogin extends React.Component {
    componentDidMount() {
        const {
            history, location, Logout, AuthenticateMobileConnect,
        } = this.props;
        const search = queryString.parse( location.search );

        if ( search.logout ) {
            Logout();
            history.push( { search: "" } );
        }
        /* MobileConnect callback */
        const { code, state } = search;
        if ( code && state ) {
            AuthenticateMobileConnect( { code, state } );
        }
    }

    render() {
        const {
            isAuthenticated,
            location,
        } = this.props;

        if ( isAuthenticated ) {
            const search = queryString.parse( location.search );
            return <Redirect to={ search.redirect || "/" } />;
        }
        return (
            <div id="login-page" className="ui middle aligned center aligned grid">
                <div className="column four wide">
                    <div className="row">
                        <img src="https://www.copyrightdelta.com/images/Asset-8.svg" id="logo" alt="Copyright Delta" />
                    </div>
                    <Segment style={ { paddingTop: 0 } }>
                        <Tab
                            panes={ panes( ) }
                            className="login-tabs"
                            menu={ { secondary: true, pointing: true } }
                        />

                    </Segment>

                </div>
            </div>

        );
    }
}
PageLogin.propTypes = {
    isAuthenticated: bool.isRequired,
    Logout: func.isRequired,
    history: shape().isRequired,
    location: shape().isRequired,
};
const mSTP = ( {
    app: {
        isAuthenticated,
        errors,
        loggingIn,
    },
} ) => ( {
    isAuthenticated, errors, loggingIn,
} );

const mDTP = dispatch => ( {
    Login: values => dispatch( actions.login( values ) ),
    Logout: () => dispatch( actions.logout() ),
    AuthenticateMobileConnect: values => dispatch( actions.authenticateMobileConnect( values ) ),
} );

export default connect( mSTP, mDTP )( PageLogin );
