/* eslint-disable jsx-a11y/click-events-have-key-events */
import React from "react";
import awsLogo from "assets/images/aws-logo.png";
import azureLogo from "assets/images/azure-logo.png";
import doLogo from "assets/images/do-logo.png";
import googleCloudLogo from "assets/images/google-logo.png";
import fed4fireLogo from "assets/images/fed4fire-logo.png";
import classNames from "classnames";
import {
    Image,
} from "semantic-ui-react";
import {
    shape, bool, func, node,
} from "prop-types";

const getImage = ( value ) => {
    switch ( value ) {
        case "aws":
            return awsLogo;
        case "azure":
            return azureLogo;
        case "google":
            return googleCloudLogo;
        case "fed4fire":
            return fed4fireLogo;
        case "digitalocean":
            return doLogo;
        default:
            return "";
    }
};
const StorageOption = ( {
    selected, onSelect, value, children,
} ) => (
    <div
        className={ classNames( "storage-option", selected && "selected" ) }
        onClick={ () => onSelect( value ) }
    >
        <Image src={ getImage( value ) } size="small" />
        {children}
    </div>
);
StorageOption.propTypes = {
    selected: bool,
    onSelect: func,
    value: shape(),
    children: node,
};
StorageOption.defaultProps = {
    selected: false,
    onSelect: () => {},
    value: null,
    children: null,
};
export default StorageOption;
