/* eslint-disable complexity */
import React, { Component } from "react";
import { bool, func, shape } from "prop-types";
import {
    Segment, SegmentGroup, Form, Button, Header,
} from "semantic-ui-react";

import countryList from "iso-3166-country-list";

const countries = countryList.map( country => ( { key: country.code, text: country.name, value: country.code } ) );

class ArtistTab extends Component {
    constructor( props ) {
        super( props );

        this.state = {
            country: null,
            isni: null,
            ipi: null,
            name: null,
            type: null,
        };
    }

    save=() => {
        const {
            country, isni, ipi, name, type,
        } = this.state;
        const { onSave, artist } = this.props;

        onSave( {
            country: country || artist.country,
            isni: isni || artist.isni,
            ipi: ipi || artist.ipi,
            name: name || artist.name,
            type: type || artist.type,
        } );
    }

    render() {
        const { saving, artist } = this.props;
        const {
            isni, ipi, name,
        } = this.state;
        return (
            <div className="page-content">
                <SegmentGroup>
                    <Segment>
                        <Header as="h4">Artist</Header>

                    </Segment>
                    <Segment className="form-body">
                        <Form>
                            <Form.Group widths="equal">
                                <Form.Input
                                    label="Name"
                                    required
                                    value={ artist && !name ? artist.name : name }
                                    placeholder="Name"
                                    onChange={ ( e ) => {
                                        this.setState( { name: e.currentTarget.value } );
                                    } }
                                />

                                <Form.Select
                                    required
                                    label="Country"
                                    defaultValue={ artist ? artist.country : null }
                                    placeholder="Select a country"
                                    selection
                                    options={ countries }
                                    onChange={ ( e, { value } ) => {
                                        this.setState( { country: value } );
                                    } }
                                />
                                <Form.Select
                                    required
                                    label="Type"
                                    defaultValue={ artist ? artist.type : null }
                                    placeholder="Select a type"
                                    selection
                                    options={ [
                                        { key: "INDIVIDUAL", text: "Individual", value: "INDIVIDUAL" },
                                        { key: "ORCHESTRA", text: "Orchestra", value: "ORCHESTRA" },
                                        { key: "GROUP", text: "Group", value: "GROUP" },
                                        { key: "FICTIONAL_CHARACTER", text: "Fictional character", value: "FICTIONAL_CHARACTER" },
                                        { key: "OTHER", text: "Other", value: "OTHER" },
                                    ] }
                                    onChange={ ( e, { value } ) => {
                                        this.setState( { country: value } );
                                    } }
                                />
                            </Form.Group>
                            <Form.Group widths="equal">

                                <Form.Input
                                    label="ISNI"
                                    value={ artist && !isni ? artist.isni : isni }
                                    placeholder="ISNI"
                                    onChange={ ( e ) => {
                                        this.setState( { isni: e.currentTarget.value } );
                                    } }
                                />
                                <Form.Input
                                    label="IPI"
                                    value={ artist && !ipi ? artist.ipi : ipi }
                                    placeholder="IPI"
                                    onChange={ ( e ) => {
                                        this.setState( { ipi: e.currentTarget.value } );
                                    } }
                                />

                            </Form.Group>

                        </Form>
                    </Segment>
                </SegmentGroup>

                <Button

                    className="primary"
                    loading={ saving }
                    onClick={ this.save }
                >
                    {artist ? "Save" : "Confirm"}

                </Button>
            </div>
        );
    }
}
ArtistTab.propTypes = {
    onSave: func.isRequired,
    saving: bool.isRequired,
    artist: shape( {} ),
};
ArtistTab.defaultProps = {
    artist: null,
};
export default ArtistTab;
