import React from "react";
import {
    func, arrayOf, shape, bool,
} from "prop-types";
import {
    Header, Icon, Button, Grid,
} from "semantic-ui-react";
import { connect } from "react-redux";
import RegisterRecordingForm from "../../forms/register/song/recording/RegisterRecordingForm";
import { actions } from "./duck";

const RecordingsTab = ( {
    account,
    registeredRecording,
    registerRecordingProcessing,
    SearchAccounts,
    RegisterRecording,
    artists,
    labels,
    Reset,
} ) => {
    if ( registeredRecording ) {
        return (
            <div className="page-content">
                <div className="ui middle aligned center aligned grid">
                    <Grid.Row>
                        <Header as="h2" icon>
                            <Icon name="check" />
    Submitted
                            <Header.Subheader>
    Awaiting further verification by other parties. (unless solo created)
                            </Header.Subheader>
                        </Header>

                    </Grid.Row>
                    <Grid.Row>
                        <Button

                            className="primary"
                            icon
                            labelPosition="right"
                            onClick={ Reset }
                        >
      Register another
                            <Icon name="right arrow" />
                        </Button>
                    </Grid.Row>
                </div>

            </div>
        );
    }
    return (
        <div className="page-content">
            <RegisterRecordingForm
                account={ account }
                searchAccounts={ SearchAccounts }
                onRegister={ RegisterRecording }
                artists={ artists }
                labels={ labels }
                registerRecordingProcessing={ registerRecordingProcessing }
            />
        </div>
    );
};
RecordingsTab.propTypes = {
    registeredRecording: bool.isRequired,
    registerRecordingProcessing: bool.isRequired,
    account: shape( {} ).isRequired,
    RegisterRecording: func.isRequired,
    SearchAccounts: func.isRequired,
    Reset: func.isRequired,
    artists: arrayOf().isRequired,
    labels: arrayOf().isRequired,
};

const mSTP = ( {
    app: {
        account,
    },
    registerSongs: {
        artists,
        labels,
        errors,
        registeredRecording,
        registerRecordingProcessing,
    },
} ) => ( {
    account,
    artists,
    labels,
    errors,
    registeredRecording,
    registerRecordingProcessing,
} );

const mDTP = dispatch => ( {
    RegisterRecording: payload => dispatch( actions.registerRecording( payload ) ),
    Reset: payload => dispatch( actions.reset( payload ) ),
    SearchAccounts: payload => dispatch( actions.searchAccounts( payload ) ),
} );

export default connect( mSTP, mDTP )( RecordingsTab );
