import {
    call,
    put,
    takeEvery,
} from "redux-saga/effects";
import axios from "axios";

import errorSelectors from "components/error/duck/Selectors";
import errorTypes from "components/error/duck/Types";
import types from "./Types";

function* load( ) {
    try {
        const { data: works } = yield call( axios.get, "/works/unverified" );
        const { data: recordings } = yield call( axios.get, "/recordings/unverified" );

        yield put( { type: types.LOAD_SUCCESS, payload: { works, recordings } } );
    } catch ( error ) {
        yield put( {
            type: errorSelectors.errorType( error, types.LOAD_ERROR, errorTypes.APP_ERROR ),
            payload: errorSelectors.errorPayload( error ),
        } );
    }
}
function* verify( { payload } ) {
    try {
        yield call( axios.patch, `/${ payload.type }/${ payload.id }/verify` );
        yield put( { type: types.VERIFY_SUCCESS, payload } );
    } catch ( error ) {
        yield put( {
            type: errorSelectors.errorType( error, types.VERIFY_ERROR, errorTypes.APP_ERROR ),
            payload: errorSelectors.errorPayload( error ),
        } );
    }
}
export default function* main() {
    yield takeEvery( types.LOAD, load );
    yield takeEvery( types.VERIFY, verify );
}
