/* eslint-disable prefer-destructuring */
import {
    call,
    put,
    takeEvery,
} from "redux-saga/effects";
import axios from "axios";

import errorSelectors from "components/error/duck/Selectors";
import errorTypes from "components/error/duck/Types";
import types from "./Types";

function* registerWork( { payload } ) {
    try {
        const splits = {};
        Array.from( payload.splits.entries() ).forEach( ( entry ) => {
            splits[ entry[ 0 ] ] = entry[ 1 ];
        } );
        const roles = {};
        Array.from( payload.roles.entries() ).forEach( ( entry ) => {
            roles[ entry[ 0 ] ] = entry[ 1 ];
        } );
        const newPayload = {
            ...payload,
            splits,
            roles,
        };
        const { data } = yield call( axios.post, "/works", newPayload );
        yield put( { type: types.REGISTER_WORK_SUCCESS, payload: data } );
    } catch ( error ) {
        yield put( {
            type: errorSelectors.errorType( error, types.REGISTER_WORK_ERROR, errorTypes.APP_ERROR ),
            payload: errorSelectors.errorPayload( error ),
        } );
    }
}

function* searchAccounts( { payload } ) {
    try {
        const { data } = yield call( axios.get, `/${ payload.type }?name=${ payload.query }` );
        yield put( { type: types.SEARCH_ACCOUNTS_SUCCESS, payload: { accounts: data, type: payload.type } } );
    } catch ( error ) {
        yield put( {
            type: errorSelectors.errorType( error, types.SEARCH_ACCOUNTS_ERROR, errorTypes.APP_ERROR ),
            payload: errorSelectors.errorPayload( error ),
        } );
    }
}
function* registerRecording( { payload } ) {
    try {
        const splits = {};
        Array.from( payload.splits.entries() ).forEach( ( entry ) => {
            splits[ entry[ 0 ] ] = entry[ 1 ];
        } );
        const roles = {};
        Array.from( payload.roles.entries() ).forEach( ( entry ) => {
            roles[ entry[ 0 ] ] = entry[ 1 ];
        } );
        const newPayload = {
            ...payload,
            splits,
            roles,
        };
        const { data } = yield call( axios.post, "/recordings", newPayload );
        yield put( { type: types.REGISTER_RECORDING_SUCCESS, payload: data } );
    } catch ( error ) {
        yield put( {
            type: errorSelectors.errorType( error, types.REGISTER_RECORDING_ERROR, errorTypes.APP_ERROR ),
            payload: errorSelectors.errorPayload( error ),
        } );
    }
}
function* reset( ) {
    try {
        yield put( { type: types.RESET_SUCCESS } );
    } catch ( error ) {
        yield put( {
            type: errorSelectors.errorType( error, types.RESET_ERROR, errorTypes.APP_ERROR ),
            payload: errorSelectors.errorPayload( error ),
        } );
    }
}
function* registerBatch( { payload } ) {
    try {
        const { type, resources } = payload;
        const newPayload = {};
        newPayload[ type ] = resources.map( ( resource ) => {
            const splits = {};
            Array.from( resource.splits.entries() ).forEach( ( entry ) => {
                splits[ entry[ 0 ] ] = entry[ 1 ];
            } );
            const roles = {};
            Array.from( resource.roles.entries() ).forEach( ( entry ) => {
                roles[ entry[ 0 ] ] = entry[ 1 ];
            } );
            return { ...resource, splits, roles };
        } );
        const { data } = yield call( axios.post, `/${ type }/batch`, newPayload );
        yield put( { type: types.REGISTER_BATCH_SUCCESS, payload: data } );
    } catch ( error ) {
        yield put( {
            type: errorSelectors.errorType( error, types.REGISTER_BATCH_ERROR, errorTypes.APP_ERROR ),
            payload: errorSelectors.errorPayload( error ),
        } );
    }
}
export default function* main() {
    yield takeEvery( types.REGISTER_WORK, registerWork );
    yield takeEvery( types.REGISTER_BATCH, registerBatch );
    yield takeEvery( types.SEARCH_ACCOUNTS, searchAccounts );
    yield takeEvery( types.RESET, reset );
    yield takeEvery( types.REGISTER_RECORDING, registerRecording );
}
