/* eslint-disable complexity */
import React from "react";
import {
    Form, Button, Header, Grid,
} from "semantic-ui-react";
import { Page } from "components";
import StorageOption from "./StorageOption";

// eslint-disable-next-line react/prefer-stateless-function
class PageSettings extends React.Component {
    constructor( props ) {
        super( props );
        this.state = {
            selectedStorage: "aws",
        };
    }

    render() {
        const { selectedStorage } = this.state;
        return (
            <Page>
                <div className="page-content">
                    <Header as="h3">Host my node on</Header>
                    <Grid verticalAlign="middle">
                        <Grid.Row>
                            <Grid.Column key={ 1 } width={ 3 }>
                                <StorageOption
                                    selected={ selectedStorage === "aws" }
                                    value="aws"
                                    onSelect={ ( value ) => {
                                        this.setState( { selectedStorage: value } );
                                    } }
                                />

                            </Grid.Column>
                            <Grid.Column key={ 2 } width={ 3 }>
                                <StorageOption
                                    selected={ selectedStorage === "azure" }
                                    value="azure"
                                    onSelect={ ( value ) => {
                                        this.setState( { selectedStorage: value } );
                                    } }
                                />
                            </Grid.Column>
                            <Grid.Column key={ 3 } width={ 3 }>
                                <StorageOption
                                    selected={ selectedStorage === "digitalocean" }
                                    value="digitalocean"
                                    onSelect={ ( value ) => {
                                        this.setState( { selectedStorage: value } );
                                    } }
                                />
                            </Grid.Column>
                            <Grid.Column key={ 4 } width={ 3 }>
                                <StorageOption
                                    selected={ selectedStorage === "google" }
                                    value="google"
                                    onSelect={ ( value ) => {
                                        this.setState( { selectedStorage: value } );
                                    } }
                                />
                            </Grid.Column>
                            <Grid.Column key={ 5 } width={ 3 }>
                                <StorageOption
                                    selected={ selectedStorage === "fed4fire" }
                                    value="fed4fire"
                                    onSelect={ ( value ) => {
                                        this.setState( { selectedStorage: value } );
                                    } }
                                >
                                    <span style={ { fontWeight: "bold" } }>Test network for your experimentation</span>

                                </StorageOption>
                            </Grid.Column>
                        </Grid.Row>

                    </Grid>
                    <Form>
                        <Form.Group></Form.Group>

                        <Button
                            className="primary"
                            icon
                            onClick={ () => {} }
                        >
      Save

                        </Button>
                    </Form>
                </div>
            </Page>
        );
    }
}
PageSettings.propTypes = {

};
PageSettings.defaultProps = {

};
export default PageSettings;
