import types from "./Types";

const init = payload => ( { type: types.INIT, payload } );
const getProfile = payload => ( { type: types.GET_PROFILE, payload } );

const login = payload => ( { type: types.LOGIN, payload } );
const register = payload => ( { type: types.REGISTER, payload } );
const logout = () => ( { type: types.LOGOUT } );

const authenticateMobileConnect = payload => ( { type: types.AUTH_MOBILECONNECT, payload } );

export default {
    init,
    getProfile,
    login,
    register,
    logout,
    authenticateMobileConnect,
};
