/* eslint-disable complexity */
import React, { Component } from "react";
import { bool, func, shape } from "prop-types";
import {
    Segment, SegmentGroup, Form, Button, Header,
} from "semantic-ui-react";

import countryList from "iso-3166-country-list";

const countries = countryList.map( country => ( { key: country.code, text: country.name, value: country.code } ) );

class LabelTab extends Component {
    constructor( props ) {
        super( props );

        this.state = {
            country: null,
            isni: null,
            ipi: null,
            name: null,
            type: null,
            code: null,
        };
    }

    save=() => {
        const {
            country, isni, ipi, name, type, code,
        } = this.state;
        const { onSave, label } = this.props;
        onSave( {
            country: country || label.country,
            code: code || label.code,
            isni: isni || label.isni,
            ipi: ipi || label.ipi,
            name: name || label.name,
            type: type || label.type,
        } );
    }

    render() {
        const { saving, label } = this.props;
        const {
            isni, ipi, name,
        } = this.state;
        return (
            <div className="page-content">
                <SegmentGroup>
                    <Segment>
                        <Header as="h4">Label</Header>

                    </Segment>
                    <Segment className="form-body">
                        <Form>
                            <Form.Group widths="equal">
                                <Form.Input
                                    label="Name"
                                    required
                                    value={ label && !name ? label.name : name }
                                    placeholder="Name"
                                    onChange={ ( e ) => {
                                        this.setState( { name: e.currentTarget.value } );
                                    } }
                                />

                                <Form.Select
                                    required
                                    label="Country"
                                    defaultValue={ label ? label.country : null }
                                    placeholder="Select a country"
                                    selection
                                    options={ countries }
                                    onChange={ ( e, { value } ) => {
                                        this.setState( { country: value } );
                                    } }
                                />
                                <Form.Select
                                    required
                                    label="Type"
                                    defaultValue={ label ? label.type : null }
                                    placeholder="Select a type"
                                    selection
                                    options={ [
                                        { key: "IMPRINT", text: "Imprint", value: "IMPRINT" },
                                        { key: "ORIGINAL_PRODUCTION", text: "Original production", value: "ORIGINAL_PRODUCTION" },
                                        { key: "BOOTLEG_PRODUCTION", text: "Bootleg production", value: "BOOTLEG_PRODUCTION" },
                                        { key: "REISSUE_PRODUCTION", text: "Reissue production", value: "REISSUE_PRODUCTION" },
                                        { key: "DISTRIBUTOR", text: "Distributor", value: "DISTRIBUTOR" },
                                        { key: "HOLDING", text: "Holding", value: "HOLDING" },
                                        { key: "RIGHTS_SOCIETY", text: "Rights society", value: "RIGHTS_SOCIETY" },
                                    ] }
                                    onChange={ ( e, { value } ) => {
                                        this.setState( { country: value } );
                                    } }
                                />
                            </Form.Group>
                            <Form.Group widths="equal">

                                <Form.Input
                                    label="ISNI"
                                    value={ label && !isni ? label.isni : isni }
                                    placeholder="ISNI"
                                    onChange={ ( e ) => {
                                        this.setState( { isni: e.currentTarget.value } );
                                    } }
                                />
                                <Form.Input
                                    label="IPI"
                                    value={ label && !ipi ? label.ipi : ipi }
                                    placeholder="IPI"
                                    onChange={ ( e ) => {
                                        this.setState( { ipi: e.currentTarget.value } );
                                    } }
                                />

                            </Form.Group>

                        </Form>
                    </Segment>
                </SegmentGroup>

                <Button

                    className="primary"
                    loading={ saving }
                    onClick={ this.save }
                >
                    {label ? "Save" : "Confirm"}

                </Button>
            </div>
        );
    }
}
LabelTab.propTypes = {
    onSave: func.isRequired,
    saving: bool.isRequired,
    label: shape( {} ),
};
LabelTab.defaultProps = {
    label: null,
};
export default LabelTab;
