/* eslint-disable complexity */
import { combineReducers } from "redux";

import types from "./Types";

const defaultState = {
    authors: [],
    publishers: [],
    artists: [],
    labels: [],
    registeredWork: false,
    registeredRecording: false,
    registeredBatch: false,
    registerWorkProcessing: false,
    registerRecordingProcessing: false,
    registerBatchProcessing: false,
};
const authors = ( state = defaultState.authors, { type, payload } ) => {
    if ( type === types.SEARCH_ACCOUNTS_SUCCESS && payload.type === "authors" ) {
        return payload.accounts;
    }
    return state;
};
const publishers = ( state = defaultState.publishers, { type, payload } ) => {
    if ( type === types.SEARCH_ACCOUNTS_SUCCESS && payload.type === "publishers" ) {
        return payload.accounts;
    }
    return state;
};
const artists = ( state = defaultState.artists, { type, payload } ) => {
    if ( type === types.SEARCH_ACCOUNTS_SUCCESS && payload.type === "artists" ) {
        return payload.accounts;
    }
    return state;
};
const labels = ( state = defaultState.labels, { type, payload } ) => {
    if ( type === types.SEARCH_ACCOUNTS_SUCCESS && payload.type === "labels" ) {
        return payload.accounts;
    }
    return state;
};
const registeredWork = ( state = defaultState.registeredWork, { type } ) => {
    if ( type === types.REGISTER_WORK_SUCCESS ) {
        return true;
    }
    if ( type === types.RESET_SUCCESS ) {
        return false;
    }
    return state;
};
const registerWorkProcessing = ( state = defaultState.registerWorkProcessing, { type } ) => {
    if ( type === types.REGISTER_WORK_SUCCESS || type === types.REGISTER_WORK_ERROR ) {
        return false;
    }
    if ( type === types.REGISTER_WORK ) {
        return true;
    }
    return state;
};

const registeredRecording = ( state = defaultState.registeredRecording, { type } ) => {
    if ( type === types.REGISTER_RECORDING_SUCCESS ) {
        return true;
    }
    if ( type === types.RESET_SUCCESS ) {
        return false;
    }
    return state;
};
const registerRecordingProcessing = ( state = defaultState.registerRecordingProcessing, { type } ) => {
    if ( type === types.REGISTER_RECORDING_SUCCESS || type === types.REGISTER_WORK_ERROR ) {
        return false;
    }
    if ( type === types.REGISTER_RECORDING ) {
        return true;
    }
    return state;
};

const registeredBatch = ( state = defaultState.registeredBatch, { type } ) => {
    if ( type === types.REGISTER_BATCH_SUCCESS ) {
        return true;
    }
    if ( type === types.RESET_SUCCESS ) {
        return false;
    }
    return state;
};
const registerBatchProcessing = ( state = defaultState.registerBatchProcessing, { type } ) => {
    if ( type === types.REGISTER_BATCH_SUCCESS || type === types.REGISTER_BATCH_ERROR ) {
        return false;
    }
    if ( type === types.REGISTER_BATCH ) {
        return true;
    }
    return state;
};
export default combineReducers( {
    authors,
    publishers,
    artists,
    labels,
    registeredWork,
    registerWorkProcessing,
    registeredRecording,
    registerRecordingProcessing,
    registeredBatch,
    registerBatchProcessing,
} );
