import React from "react";
import {
    func, arrayOf, shape, bool,
} from "prop-types";
import {
    Header, Icon, Button, Grid,
} from "semantic-ui-react";
import { connect } from "react-redux";
import RegisterWorkForm from "../../forms/register/song/work/RegisterWorkForm";
import { actions } from "./duck";

const WorksTab = ( {
    account,
    registeredWork,
    registerWorkProcessing,
    SearchAccounts,
    RegisterWork,
    authors,
    publishers,
    Reset,
} ) => {
    if ( registeredWork ) {
        return (
            <div className="page-content">
                <div className="ui middle aligned center aligned grid">
                    <Grid.Row>
                        <Header as="h2" icon>
                            <Icon name="check" />
    Submitted
                            <Header.Subheader>
    Awaiting further verification by other parties. (unless solo created)
                            </Header.Subheader>
                        </Header>

                    </Grid.Row>
                    <Grid.Row>
                        <Button

                            className="primary"
                            icon
                            labelPosition="right"
                            onClick={ Reset }
                        >
      Register another
                            <Icon name="right arrow" />
                        </Button>
                    </Grid.Row>
                </div>

            </div>
        );
    }
    return (
        <div className="page-content">
            <RegisterWorkForm
                account={ account }
                searchAccounts={ SearchAccounts }
                onRegister={ RegisterWork }
                authors={ authors }
                publishers={ publishers }
                registerWorkProcessing={ registerWorkProcessing }
            />
        </div>
    );
};

WorksTab.propTypes = {
    registeredWork: bool.isRequired,
    registerWorkProcessing: bool.isRequired,
    account: shape( {} ).isRequired,
    RegisterWork: func.isRequired,
    SearchAccounts: func.isRequired,
    Reset: func.isRequired,
    authors: arrayOf().isRequired,
    publishers: arrayOf().isRequired,
};

const mSTP = ( {
    app: {
        account,
    },
    registerSongs: {
        authors,
        publishers,
        errors,
        registeredWork,
        registerWorkProcessing,
    },
} ) => ( {
    account,
    authors,
    publishers,
    errors,
    registeredWork,
    registerWorkProcessing,
} );

const mDTP = dispatch => ( {
    RegisterWork: payload => dispatch( actions.registerWork( payload ) ),
    Reset: payload => dispatch( actions.reset( payload ) ),
    SearchAccounts: payload => dispatch( actions.searchAccounts( payload ) ),
} );

export default connect( mSTP, mDTP )( WorksTab );
