/* eslint-disable complexity */
import { combineReducers } from "redux";

import types from "./Types";

const defaultState = {
    errors: {},
    workVerificationRequests: [],
    recordingVerificationRequests: [],
    verifying: false,
};

const workVerificationRequests = ( state = defaultState.workVerificationRequests, { type, payload } ) => {
    if ( type === types.LOAD_SUCCESS ) {
        return payload.works;
    }
    if ( type === types.VERIFY_SUCCESS ) {
        return state.filter( a => a.id !== payload.id );
    }
    return state;
};
const recordingVerificationRequests = ( state = defaultState.recordingVerificationRequests, { type, payload } ) => {
    if ( type === types.LOAD_SUCCESS ) {
        return payload.recordings;
    }
    if ( type === types.VERIFY_SUCCESS ) {
        return state.filter( a => a.id !== payload.id );
    }
    return state;
};
const errors = ( state = defaultState.errors, { type, payload } ) => {
    if ( type === types.LOAD || type === types.VERIFY ) {
        return {};
    }
    if ( type === types.LOAD_ERROR || type === types.VERIFY_ERROR ) {
        return payload;
    }
    return state;
};

const verifying = ( state = defaultState.verifying, { type } ) => {
    if ( type === types.VERIFY ) {
        return true;
    }
    if ( type === types.VERIFY_SUCCESS || type === types.VERIFY_ERROR ) {
        return false;
    }
    return state;
};

export default combineReducers( {
    errors,
    verifying,
    workVerificationRequests,
    recordingVerificationRequests,
} );
