/* eslint-disable complexity */
import React from "react";
import {
    Table, Icon, Header,
} from "semantic-ui-react";
import { VerificationStatus } from "components";
import { arrayOf } from "prop-types";

const StatusTab = ( {
    workVerificationRequests, recordingVerificationRequests, account,
} ) => {
    const requests = [ ...workVerificationRequests, ...recordingVerificationRequests ].filter( request => request.verifiedBy.find( entry => entry.account.id === account.id ) ).sort( ( a, b ) => a.createdAt - b.createdAt );
    return (
        <div className="page-content">
            {requests.map( ( request, index ) => <VerificationStatus verificationRequest={ request } collapsed={ index !== 0 } /> )}
        </div>
    );
};
StatusTab.propTypes = {
    workVerificationRequests: arrayOf().isRequired,
    recordingVerificationRequests: arrayOf().isRequired,
};

export default StatusTab;
