/* eslint-disable complexity */
/* eslint-disable no-param-reassign */
/* eslint-disable no-shadow */
import React from "react";
import {
    func, bool,
} from "prop-types";
import {
    Header, Icon, Button, SegmentGroup, Segment, Grid,
} from "semantic-ui-react";
import classnames from "classnames";
import { connect } from "react-redux";
import { actions } from "../duck";
import UploadBatch from "./UploadBatch";
import StyledOutput from "./StyledOutput";
import RawOutput from "./RawOutput";

const propertyIndexMap = {
    works: {
        resourceId: 0,
        title: 1,
        iswc: 2,
        author: 3,
        role: 4,
        publisher: 5,
        split: 6,
        createdIn: 7,
        territories: 8,
    },
    recordings: {
        resourceId: 0,
        title: 1,
        creditedArtist: 2,
        versionTitle: 3,
        isrc: 4,
        duration: 5,
        artist: 6,
        role: 7,
        label: 8,
        split: 9,
        createdIn: 10,
        territories: 11,
    },

};
class BatchTab extends React.Component {
    constructor( props ) {
        super( props );
        this.state = {
            collapsed: false,
            type: null,
            data: [],
            rawData: [],
            view: "styled",
        };
    }

    isFormValid=() => {
        const { data } = this.state;
        if ( !data ) {
            return false;
        }
        return true;
    }

    parseWorks = ( rows ) => {
        try {
            const dataWithoutHeader = rows.map( row => row.data ).slice( 1 );
            const works = [];

            dataWithoutHeader.forEach( ( row ) => {
                const resourceId = parseInt( row[ propertyIndexMap.works.resourceId ] );
                if ( !resourceId ) return;
                const resourceIndex = resourceId - 1;
                const title = row[ propertyIndexMap.works.title ];
                const iswc = row[ propertyIndexMap.works.iswc ];
                const author = row[ propertyIndexMap.works.author ];
                const roles = row[ propertyIndexMap.works.role ].split( "," );
                const publisher = row[ propertyIndexMap.works.publisher ];
                const split = parseFloat( row[ propertyIndexMap.works.split ] );
                const createdIn = row[ propertyIndexMap.works.createdIn ];
                const territories = row[ propertyIndexMap.works.territories ];

                let work = works[ resourceIndex ];
                if ( !work ) {
                    work = {
                        resourceId,
                        title,
                        iswc,
                        createdIn: createdIn.split( "," ),
                        territories: territories.split( "," ),
                        authors: [],
                        roles: new Map(),
                        publishers: [],
                        splits: new Map(),
                    };
                    works[ resourceIndex ] = work;
                }
                if ( author ) {
                    works[ resourceIndex ].authors = [ ...work.authors, author ];
                    works[ resourceIndex ].splits.set( author, split );
                    works[ resourceIndex ].roles.set( author, roles );
                }
                if ( publisher ) {
                    works[ resourceIndex ].publishers = [ ...work.publishers, publisher ];
                    works[ resourceIndex ].splits.set( publisher, split );
                }
            } );
            this.setState( {
                rawData: dataWithoutHeader,
                data: works,
                type: "works",
            } );
        } catch ( error ) {
            console.log( error );
        }
    }

    parseRecordings = ( rows ) => {
        try {
            const dataWithoutHeader = rows.map( r => r.data ).slice( 1 );
            const recordings = [];
            dataWithoutHeader.forEach( ( row ) => {
                const resourceId = parseInt( row[ propertyIndexMap.recordings.resourceId ] );
                if ( !resourceId ) return;
                const resourceIndex = resourceId - 1;
                const title = row[ propertyIndexMap.recordings.title ];
                const creditedArtist = row[ propertyIndexMap.recordings.creditedArtist ];
                const versionTitle = row[ propertyIndexMap.recordings.versionTitle ];
                const isrc = row[ propertyIndexMap.recordings.isrc ];
                const duration = row[ propertyIndexMap.recordings.duration ];
                const artist = row[ propertyIndexMap.recordings.artist ];
                const role = row[ propertyIndexMap.recordings.role ];
                const label = row[ propertyIndexMap.recordings.label ];
                const split = parseFloat( row[ propertyIndexMap.recordings.split ] );
                const createdIn = row[ propertyIndexMap.recordings.createdIn ];
                const territories = row[ propertyIndexMap.recordings.territories ];

                let recording = recordings[ resourceIndex ];
                if ( !recording ) {
                    recording = {
                        resourceId,
                        title,
                        creditedArtist,
                        versionTitle,
                        isrc,
                        duration,
                        createdIn: createdIn.split( "," ),
                        territories: territories.split( "," ),
                        additionalArtists: [],
                        roles: new Map(),
                        labels: [],
                        splits: new Map(),
                    };
                    recordings[ resourceIndex ] = recording;
                }
                if ( artist ) {
                    recordings[ resourceIndex ].additionalArtists = [ ...recording.additionalArtists, artist ];
                    recordings[ resourceIndex ].splits.set( artist, split );
                    recordings[ resourceIndex ].roles.set( artist, role );
                }
                if ( label ) {
                    recordings[ resourceIndex ].labels = [ ...recording.labels, label ];
                    recordings[ resourceIndex ].splits.set( label, split );
                }
            } );
            this.setState( {
                rawData: dataWithoutHeader,
                data: recordings,
                type: "recordings",
            } );
        } catch ( error ) {
            console.log( error );
        }
    }

    handleOnError = ( err, file, inputElem, reason ) => {
        console.log( err );
    }

    handleOnRemoveFile = ( ) => {
        this.setState( {
            rawData: [],
            data: [],
        } );
    }

    register=() => {
        const { RegisterBatch } = this.props;
        const { data, type } = this.state;
        RegisterBatch( { resources: data, type } );
    }

    render() {
        const {
            collapsed, data, rawData, view, type,
        } = this.state;
        const {
            registerBatchProcessing, registeredBatch, account, Reset,
        } = this.props;
        if ( registeredBatch ) {
            return (
                <div className="page-content">
                    <div className="ui middle aligned center aligned grid">
                        <Grid.Row>
                            <Header as="h2" icon>
                                <Icon name="check" />
        Submitted
                                <Header.Subheader>
        Awaiting further verification by other parties. (unless solo created)
                                </Header.Subheader>
                            </Header>

                        </Grid.Row>
                        <Grid.Row>
                            <Button

                                className="primary"
                                icon
                                labelPosition="right"
                                onClick={ Reset }
                            >
          Register another
                                <Icon name="right arrow" />
                            </Button>
                        </Grid.Row>
                    </div>

                </div>
            );
        }
        return (
            <div className="page-content">
                <Grid>
                    <Grid.Row>
                        {( account.author || account.publisher ) && (
                            <Grid.Column key={ 1 } width={ 8 }>
                                <UploadBatch
                                    title="Upload Work Batch"
                                    onUpload={ this.parseWorks }
                                    onRemove={ this.handleOnRemoveFile }
                                    onError={ this.handleOnError }
                                />
                            </Grid.Column>
                        )}
                        {( account.artist || account.label )
                        && (
                            <Grid.Column key={ 2 } width={ 8 }>
                                <UploadBatch
                                    title="Upload Recording Batch"
                                    onUpload={ this.parseRecordings }
                                    onRemove={ this.handleOnRemoveFile }
                                    onError={ this.handleOnError }
                                />
                            </Grid.Column>
                        )}
                    </Grid.Row>
                    <Grid.Row>
                        <Grid.Column key={ 1 } width={ 16 }>
                            <SegmentGroup>
                                <Segment style={ { cursor: "pointer" } } onClick={ () => this.setState( { collapsed: !collapsed } ) }>
                                    <Grid columns={ 2 }>
                                        <Grid.Row>
                                            <Grid.Column>
                                                <Header as="h4">Output</Header>
                                            </Grid.Column>
                                            <Grid.Column textAlign="right">

                                                <Icon name={ collapsed ? "angle up" : "angle down" } />
                                            </Grid.Column>
                                        </Grid.Row>
                                    </Grid>
                                </Segment>
                                {!collapsed && (
                                    <Segment>
                                        {{
                                            styled: <StyledOutput
                                                type={ type }
                                                data={ data }
                                                onChangeView={ ( { view } ) => {
                                                    this.setState( { view } );
                                                } }
                                            />,
                                            raw: <RawOutput
                                                type={ type }
                                                data={ rawData }
                                                onChangeView={ ( { view } ) => {
                                                    this.setState( { view } );
                                                } }
                                            />,
                                        }[ view ]}
                                    </Segment>
                                )}
                            </SegmentGroup>
                            <Button
                                className={ classnames( this.isFormValid() ? "primary" : "" ) }
                                icon
                                disabled={ !this.isFormValid() }
                                labelPosition="right"
                                loading={ registerBatchProcessing }
                                onClick={ this.register }
                            >
      Confirm
                                <Icon name="right arrow" />
                            </Button>
                        </Grid.Column>
                    </Grid.Row>
                </Grid>

            </div>
        );
    }
}
BatchTab.propTypes = {

};

const mSTP = ( {
    registerSongs: {
        registerBatchProcessing,
        registeredBatch,
        RegisterBatch,
    },
} ) => ( {
    registerBatchProcessing,
    registeredBatch,
    RegisterBatch,
} );

const mDTP = dispatch => ( {
    RegisterBatch: payload => dispatch( actions.registerBatch( payload ) ),
    Reset: payload => dispatch( actions.reset( payload ) ),
} );
BatchTab.propTypes = {
    RegisterBatch: func.isRequired,
    registeredBatch: bool.isRequired,
    registerBatchProcessing: bool.isRequired,
};
export default connect( mSTP, mDTP )( BatchTab );
