import types from "./Types";

const registerWork = payload => ( { type: types.REGISTER_WORK, payload } );
const registerBatch = payload => ( { type: types.REGISTER_BATCH, payload } );
const searchAccounts = payload => ( { type: types.SEARCH_ACCOUNTS, payload } );
const registerRecording = payload => ( { type: types.REGISTER_RECORDING, payload } );
const reset = payload => ( { type: types.RESET, payload } );

export default {
    registerWork,
    registerRecording,
    registerBatch,
    searchAccounts,
    reset,
};
