/* eslint-disable complexity */
import React from "react";

import { Page } from "components";
import { bool, func, shape } from "prop-types";
import {
    Tab, Message, Menu, Icon,
} from "semantic-ui-react";
import { connect } from "react-redux";
import { actions } from "./duck";
import AuthorTab from "./AuthorTab";
import PublisherTab from "./PublisherTab";
import ArtistTab from "./ArtistTab";
import LabelTab from "./LabelTab";

const panes = ( {
    author,
    publisher,
    artist,
    label,
    saving,
    SetAuthorProfile,
    SetPublisherProfile,
    SetArtistProfile,
    SetLabelProfile,
} ) => [
    {
        menuItem: (
            <Menu.Item key="author">
              AUTHOR
                { author && <Icon color="blue" name="check circle" />}
            </Menu.Item>
        ),
        render: () => (
            <Tab.Pane>
                <AuthorTab
                    author={ author }
                    saving={ saving }
                    onSave={ SetAuthorProfile }
                />

            </Tab.Pane>
        ),
    },
    {
        menuItem: (
            <Menu.Item key="artist">
              ARTIST
                {" "}
                { artist && <Icon color="blue" name="check circle" />}
            </Menu.Item>
        ),
        render: () => (
            <Tab.Pane>
                <ArtistTab
                    artist={ artist }
                    saving={ saving }
                    onSave={ SetArtistProfile }
                />
            </Tab.Pane>
        ),
    },
    {
        menuItem: (
            <Menu.Item key="publisher">
              PUBLISHER
                {" "}
                { publisher && <Icon color="blue" name="check circle" />}
            </Menu.Item>
        ),
        render: () => (
            <Tab.Pane>
                <PublisherTab
                    publisher={ publisher }
                    saving={ saving }
                    onSave={ SetPublisherProfile }
                />
            </Tab.Pane>
        ),
    },

    {
        menuItem: (
            <Menu.Item key="label">
              LABEL
                {" "}
                { label && <Icon color="blue" name="check circle" />}
            </Menu.Item>
        ),
        render: () => (
            <Tab.Pane>
                <LabelTab
                    label={ label }
                    saving={ saving }
                    onSave={ SetLabelProfile }
                />
            </Tab.Pane>
        ),
    },
];
// eslint-disable-next-line react/prefer-stateless-function
class PageProfile extends React.Component {
    componentDidMount() {
        const { Load } = this.props;
        Load();
    }

    render() {
        const {
            artist, label, author, publisher, SetPublisherProfile, SetAuthorProfile, SetArtistProfile, SetLabelProfile, saving,
        } = this.props;

        return (
            <Page>
                <Message style={ { paddingLeft: "45px", marginBottom: 0 } }>
Please enter your information for each role that you have. You can always add more or change it later.
                </Message>
                <Tab
                    panes={ panes( {
                        author,
                        SetAuthorProfile,
                        artist,
                        SetArtistProfile,
                        publisher,
                        SetPublisherProfile,
                        label,
                        SetLabelProfile,
                        saving,
                    } ) }
                    className="tabs"
                    menu={ { secondary: true, pointing: true } }
                />

            </Page>
        );
    }
}
PageProfile.propTypes = {
    Load: func.isRequired,
    SetAuthorProfile: func.isRequired,
    SetPublisherProfile: func.isRequired,
    SetLabelProfile: func.isRequired,
    SetArtistProfile: func.isRequired,
    author: shape( {} ),
    publisher: shape( {} ),
    artist: shape( {} ),
    label: shape( {} ),
    saving: bool.isRequired,
};
PageProfile.defaultProps = {
    author: null,
    publisher: null,
    artist: null,
    label: null,
};
const mSTP = ( {
    profile: {
        author,
        publisher,
        artist,
        label,
        saving,
    },
} ) => ( {
    author,
    publisher,
    artist,
    label,
    saving,
} );

const mDTP = dispatch => ( {
    SetAuthorProfile: payload => dispatch( actions.setAuthorProfile( payload ) ),
    SetPublisherProfile: payload => dispatch( actions.setPublisherProfile( payload ) ),
    SetArtistProfile: payload => dispatch( actions.setArtistProfile( payload ) ),
    SetLabelProfile: payload => dispatch( actions.setLabelProfile( payload ) ),
    Load: () => dispatch( actions.load() ),
} );

export default connect( mSTP, mDTP )( PageProfile );
