import React from "react";

import { Page } from "components";
import {
    Tab,
} from "semantic-ui-react";
import { arrayOf, func, shape } from "prop-types";
import { connect } from "react-redux";
import RecordingsTab from "./RecordingsTab";
import AlbumsTab from "./AlbumsTab";
import WorksTab from "./WorksTab";
import { actions } from "./duck";

const panes = ( { works, recordings } ) => [
    { menuItem: "Works", render: () => <Tab.Pane><WorksTab works={ works } /></Tab.Pane> },
    { menuItem: "Recordings", render: () => <Tab.Pane><RecordingsTab recordings={ recordings } /></Tab.Pane> },
    {
        menuItem: "Albums",
        render: () => (
            <Tab.Pane>
                <AlbumsTab />
            </Tab.Pane>
        ),
    },
];
// eslint-disable-next-line react/prefer-stateless-function
class PageCatalogue extends React.Component {
    componentDidMount() {
        const { LoadWorks, LoadRecordings } = this.props;
        LoadWorks( );
        LoadRecordings();
    }

    render() {
        const { works, recordings } = this.props;
        return (
            <Page>

                <Tab panes={ panes( { works, recordings } ) } className="tabs" menu={ { secondary: true, pointing: true } } />

            </Page>
        );
    }
}
PageCatalogue.propTypes = {
    works: arrayOf( shape( {} ) ).isRequired,
    recordings: arrayOf( shape( {} ) ).isRequired,
    LoadWorks: func.isRequired,
    LoadRecordings: func.isRequired,
};

const mSTP = ( { catalogue: { recordings, works, errors } } ) => ( {
    recordings, works, errors,
} );

const mDTP = dispatch => ( {
    LoadWorks: payload => dispatch( actions.loadWorks( payload ) ),
    LoadRecordings: payload => dispatch( actions.loadRecordings( payload ) ),
} );

export default connect( mSTP, mDTP )( PageCatalogue );
