/* eslint-disable react/prefer-stateless-function */
/* eslint-disable complexity */
import React, { Component } from "react";
import {
    Segment, Icon, Grid, Header,
} from "semantic-ui-react";
import {
    shape, bool,
} from "prop-types";
import moment from "moment";
import Verifier from "./Verifier";

class VerificationStatus extends Component {
    constructor( props ) {
        super( props );
        const { verificationRequest } = this.props;
        const dates = verificationRequest.verifiedBy.map( entry => entry.date );
        const datesByYear = new Map();
        dates.forEach( ( date ) => {
            const year = moment( date ).format( "YYYY" );
            if ( datesByYear[ year ] ) {
                datesByYear.set( year, [ ...dates[ year ], date ] );
            } else {
                datesByYear.set( year, [ date ] );
            }
        } );
        let waitingOn = [];
        if ( verificationRequest.authors ) {
            waitingOn.push( ...verificationRequest.authors );
        }
        if ( verificationRequest.publishers ) {
            waitingOn.push( ...verificationRequest.publishers );
        }
        if ( verificationRequest.additionalArtists ) {
            waitingOn.push( ...verificationRequest.additionalArtists );
        }
        if ( verificationRequest.labels ) {
            waitingOn.push( ...verificationRequest.labels );
        }
        waitingOn = waitingOn.filter( owner => !verificationRequest.verifiedBy.find( verifierEntry => verifierEntry.account.id === owner.id ) );

        const monthsBetweenNowAndLastVerification = [];
        const latestVerification = verificationRequest.verifiedBy.map( entry => entry.data ).sort( ( a, b ) => a - b )[ 0 ];
        const monthDifference = moment().diff( moment( latestVerification ), "months", true );
        for ( let i = 0; i < monthDifference; i += 1 ) {
            const month = moment().add( 0, "months" ).format( "MMMM" );
            if ( !monthsBetweenNowAndLastVerification.includes( month ) ) {
                monthsBetweenNowAndLastVerification.push( month );
            }
        }
        this.state = {
            collapsed: props.collapsed,
            datesByYear,
            waitingOn,
            monthsBetweenNowAndLastVerification,
        };
    }

    render() {
        const {
            collapsed, datesByYear, waitingOn, monthsBetweenNowAndLastVerification,
        } = this.state;
        const {
            verificationRequest,
        } = this.props;
        return (
            <Segment.Group className="verification-status-container">
                <Segment onClick={ () => this.setState( { collapsed: !collapsed } ) }>
                    <Grid columns={ 2 }>
                        <Grid.Row>
                            <Grid.Column>
                                <Header as="h4">
                                    {verificationRequest.creditedArtist ? `${ verificationRequest.creditedArtist } - ${ verificationRequest.title }` : verificationRequest.title}
                                </Header>
                            </Grid.Column>
                            <Grid.Column textAlign="right">
                                <Icon name={ collapsed ? "angle up" : "angle down" } />

                            </Grid.Column>
                        </Grid.Row>
                    </Grid>

                </Segment>
                {!collapsed
                && (
                    <Segment>
                        {Array.from( datesByYear ).map( ( [ key, value ] ) => {
                            const months = value.map( date => moment( date ).format( "MMMM" ) );
                            return (
                                <div key={ key }>
                                    <Header
                                        as="h1"
                                        className="verification-year"
                                    >
                                        {key}
                                    </Header>
                                    {[ ...new Set( months.map( month => month ) ) ].map( month => (
                                        <div key={ month }>
                                            <Header
                                                className="verification-month"
                                            >
                                                {month}
                                            </Header>
                                            {verificationRequest.verifiedBy.filter( entry => moment( entry.date ).format( "MMMM yy" ) === `${ month } ${ key }` ).map( entry => (
                                                <Verifier
                                                    key={ entry.account.id }
                                                    verifier={ entry.account }
                                                    date={ entry.date }
                                                    request={ verificationRequest }
                                                />
                                            ) )}
                                        </div>
                                    ) )}
                                    {monthsBetweenNowAndLastVerification.map( month => (
                                        <Header
                                            className="verification-month"
                                        >
                                            {month}
                                        </Header>
                                    ) )}
                                    {waitingOn.map( account => (
                                        <Verifier
                                            key={ account.id }
                                            verifier={ account }
                                            date={ null }
                                            request={ verificationRequest }
                                        />
                                    ) )}
                                </div>
                            );
                        } )}
                    </Segment>
                )}

            </Segment.Group>
        );
    }
}
VerificationStatus.defaultProps = {
    collapsed: true,
};
VerificationStatus.propTypes = {
    verificationRequest: shape( {} ).isRequired,
    collapsed: bool,
};
export default VerificationStatus;
