/* eslint-disable react/prefer-stateless-function */
import React from "react";

import { Page } from "components";
import { Tab, Grid } from "semantic-ui-react";
import MapChart from "./MapChart";
import DSPChart from "./DSPChart";
import CountryChart from "./CountryChart";
import PlaysChart from "./PlaysChart";

const segments = period => (
    <div>
        <Grid>
            <Grid.Row>
                <Grid.Column key={ 1 } width={ 8 }>
                    <DSPChart />
                </Grid.Column>
                <Grid.Column key={ 2 } width={ 8 }>
                    <CountryChart />
                </Grid.Column>
            </Grid.Row>
            <Grid.Row>
                <Grid.Column key={ 1 } width={ 16 }>
                    <PlaysChart period={ period } />
                </Grid.Column>
            </Grid.Row>
        </Grid>
    </div>
);
const panes = [
    { menuItem: "daily", render: () => <Tab.Pane>{segments( "day" )}</Tab.Pane> },
    { menuItem: "weekly", render: () => <Tab.Pane>{segments( "week" )}</Tab.Pane> },
    { menuItem: "monthly", render: () => <Tab.Pane>{segments( "month" )}</Tab.Pane> },
    { menuItem: "yearly", render: () => <Tab.Pane>{segments( "year" )}</Tab.Pane> },
    { menuItem: "map", render: () => <Tab.Pane><MapChart /></Tab.Pane> },

];
class PageDashboard extends React.Component {
    static propTypes = {
    };

    static defaultProps = {
        errors: {},
    };

    render() {
        return (
            <Page>
                <Tab panes={ panes } id="period-tabs" menu={ { secondary: true, pointing: true } } />

            </Page>
        );
    }
}

export default PageDashboard;
