/* eslint-disable react/prefer-stateless-function */
import React from "react";
import { connect } from "react-redux";
import {
    Tab,
} from "semantic-ui-react";
import { Page } from "components";
import { arrayOf, shape, func } from "prop-types";
import RecordingsTab from "./RecordingsTab";
import WorksTab from "./WorksTab";
import { actions } from "../catalogue/duck";

const panes = ( {
    account, works, recordings,
} ) => {
    const tabs = [];
    if ( account.author || account.publisher ) {
        tabs.push( { menuItem: "Works", render: () => <Tab.Pane><WorksTab works={ works } /></Tab.Pane> } );
    }
    if ( account.artist || account.label ) {
        tabs.push( { menuItem: "Recordings", render: () => <Tab.Pane><RecordingsTab recordings={ recordings } /></Tab.Pane> } );
    }

    return tabs;
};
class PageSplits extends React.Component {
    componentDidMount() {
        const { LoadWorks, LoadRecordings } = this.props;
        LoadWorks( );
        LoadRecordings( );
    }

    render() {
        const { works, recordings, account } = this.props;

        return (
            <Page>
                {account
                && (
                    <Tab
                        panes={ panes( {
                            works, recordings, account,
                        } ) }
                        className="tabs"
                        menu={ { secondary: true, pointing: true } }
                    />
                )}

            </Page>
        );
    }
}

PageSplits.propTypes = {
    works: arrayOf( shape( {} ) ).isRequired,
    recordings: arrayOf( shape( {} ) ).isRequired,
    LoadWorks: func.isRequired,
    LoadRecordings: func.isRequired,
    account: shape( {} ),

};
PageSplits.defaultProps = {
    account: null,
};
const mSTP = ( {
    app: { account },
    catalogue: {
        recordings, works, errors,
    },
} ) => ( {
    recordings, works, errors, account,
} );

const mDTP = dispatch => ( {
    LoadWorks: payload => dispatch( actions.loadWorks( payload ) ),
    LoadRecordings: payload => dispatch( actions.loadRecordings( payload ) ),
} );

export default connect( mSTP, mDTP )( PageSplits );
