import types from "./Types";

const load = payload => ( { type: types.LOAD, payload } );
const setAuthorProfile = payload => ( { type: types.SET_AUTHOR_PROFILE, payload } );
const setPublisherProfile = payload => ( { type: types.SET_PUBLISHER_PROFILE, payload } );
const setArtistProfile = payload => ( { type: types.SET_ARTIST_PROFILE, payload } );
const setLabelProfile = payload => ( { type: types.SET_LABEL_PROFILE, payload } );

export default {
    setAuthorProfile, setPublisherProfile, setArtistProfile, setLabelProfile, load,
};
