/* eslint-disable complexity */
import { combineReducers } from "redux";

import axios from "axios";
import types from "./Types";
import { types as profileTypes } from "../../pages/profile/duck";
import { types as verificationTypes } from "../../pages/verifications/duck";
import auth from "./Auth";

const defaultState = {
    isConnected: null,
    isAuthenticated: false,
    loggingIn: false,
    registering: false,
    account: null,
    roles: [],
    errors: {},
    verificationRequestsCount: 0,
    pendingVerificationRequestsCount: 0,
};

const verificationRequestsCount = ( state = defaultState.verificationRequestsCount, { type, payload } ) => {
    if ( type === types.GET_VERIFICATIONS_COUNT_SUCCESS ) {
        return payload.unverified;
    }
    if ( type === verificationTypes.VERIFY_SUCCESS ) {
        return state - 1;
    }
    return state;
};

const pendingVerificationRequestsCount = ( state = defaultState.pendingVerificationRequestsCount, { type, payload } ) => {
    if ( type === types.GET_VERIFICATIONS_COUNT_SUCCESS ) {
        return payload.pending;
    }

    return state;
};
const isConnected = ( state = defaultState.isConnected, { type } ) => {
    if ( type === types.VALIDATE_CONNECTION_SUCCESS ) {
        return true;
    }
    if ( type === types.VALIDATE_CONNECTION_ERROR ) {
        return true;
    }
    return state;
};

const isAuthenticated = ( state = defaultState.isAuthenticated, { type, payload } ) => {
    if ( type === types.INIT ) {
        const token = auth.getToken();
        if ( token ) {
            axios.defaults.headers.common.Authorization = `Bearer ${ token }`;
            return true;
        }

        return false;
    }
    if ( type === types.LOGIN_SUCCESS || type === types.REGISTER_SUCCESS || type === profileTypes.SET_AUTHOR_PROFILE_SUCCESS ) {
        axios.defaults.headers.common.Authorization = `Bearer ${ payload.token }`;
        auth.setToken( payload.token, payload.expires );
        return true;
    }
    if ( type === types.LOGOUT ) {
        axios.defaults.headers.common.Authorization = null;
        auth.removeToken();
        return false;
    }
    return state;
};

const account = ( state = defaultState.account, { type, payload } ) => {
    if ( type === types.INIT ) {
        const storedAccount = localStorage.getItem( "account" );
        if ( storedAccount ) {
            return JSON.parse( storedAccount );
        }
    }
    if ( type === types.GET_PROFILE_SUCCESS ) {
        localStorage.setItem( "account", JSON.stringify( payload ) );
        return payload;
    }
    if ( type === profileTypes.SET_AUTHOR_PROFILE_SUCCESS ) {
        const storedAccount = JSON.parse( localStorage.getItem( "account" ) );
        const updatedAccount = { ...storedAccount, author: payload.author };
        localStorage.setItem( "account", JSON.stringify( updatedAccount ) );
        return updatedAccount;
    }
    if ( type === profileTypes.SET_PUBLISHER_PROFILE_SUCCESS ) {
        const storedAccount = JSON.parse( localStorage.getItem( "account" ) );
        const updatedAccount = { ...storedAccount, publisher: payload.publisher };
        localStorage.setItem( "account", JSON.stringify( updatedAccount ) );
        return updatedAccount;
    }
    if ( type === profileTypes.SET_ARTIST_PROFILE_SUCCESS ) {
        const storedAccount = JSON.parse( localStorage.getItem( "account" ) );
        const updatedAccount = { ...storedAccount, publisher: payload.artist };
        localStorage.setItem( "account", JSON.stringify( updatedAccount ) );
        return updatedAccount;
    }
    if ( type === profileTypes.SET_LABEL_PROFILE_SUCCESS ) {
        const storedAccount = JSON.parse( localStorage.getItem( "account" ) );
        const updatedAccount = { ...storedAccount, publisher: payload.label };
        localStorage.setItem( "account", JSON.stringify( updatedAccount ) );
        return updatedAccount;
    }
    if ( type === types.LOGOUT ) {
        return null;
    }
    return state;
};

const roles = ( state = defaultState.roles, { type, payload } ) => {
    if ( type === types.INIT ) {
        const token = auth.getToken();
        if ( token ) {
            return auth.rolesFromToken( token );
        }
        return [];
    }
    if ( type === types.LOGIN_SUCCESS || type === types.REGISTER_SUCCESS
         || type === profileTypes.SET_AUTHOR_PROFILE_SUCCESS || type === profileTypes.SET_PUBLISHER_PROFILE_SUCCESS ) {
        return auth.rolesFromToken( payload.token );
    }
    if ( type === types.LOGOUT ) {
        return [];
    }
    return state;
};

const loggingIn = ( state = defaultState.loggingIn, { type } ) => {
    if ( type === types.LOGIN ) {
        return true;
    }
    if ( type === types.LOGIN_SUCCESS || type === types.LOGIN_ERROR ) {
        return false;
    }
    return state;
};
const registering = ( state = defaultState.registering, { type } ) => {
    if ( type === types.REGISTER ) {
        return true;
    }
    if ( type === types.REGISTER_SUCCESS || type === types.REGISTER_ERROR ) {
        return false;
    }
    return state;
};
const errors = ( state = defaultState.errors, { type, payload } ) => {
    if ( type === types.LOGIN ) {
        return {};
    }
    if ( type === types.LOGIN_ERROR ) {
        return payload;
    }
    return state;
};

export default combineReducers( {
    isConnected,
    isAuthenticated,
    account,
    roles,
    loggingIn,
    registering,
    errors,
    verificationRequestsCount,
    pendingVerificationRequestsCount,
} );
