/* eslint-disable complexity */
/* eslint-disable no-shadow */
/* eslint-disable react/no-array-index-key */
import React from "react";
import {
    Table, Dropdown, Loader,
} from "semantic-ui-react";
import {
    Chart,
} from "react-google-charts";
import {
    arrayOf, func, string, shape,
} from "prop-types";

const generateHeaderForType = ( type, onChangeView ) => {
    let headers = [];
    if ( type === "works" ) {
        headers = [ "Resource ID", "Title", "ISWC", "Authors", "Roles", "Publishers" ];
    } else if ( type === "recordings" ) {
        headers = [ "Resource ID", "Title", "Credited Artist", "Version Title", "ISRC", "Duration", "Artists", "Roles", "Labels" ];
    }
    return (
        <Table.Header>
            <Table.Row>
                {headers.map( h => <Table.HeaderCell>{h}</Table.HeaderCell> )}

                <Table.HeaderCell>
                    <Dropdown text="View">
                        <Dropdown.Menu>
                            <Dropdown.Item
                                text="Styled"
                                onClick={ () => onChangeView( { view: "styled" } ) }
                            />
                            <Dropdown.Item
                                text="Raw"
                                onClick={ () => onChangeView( { view: "raw" } ) }
                            />
                        </Dropdown.Menu>
                    </Dropdown>
                </Table.HeaderCell>
            </Table.Row>
        </Table.Header>

    );
};

const calculateSplits = ( resource, type, ownerType ) => {
    const data = [ [ ownerType, "Percentage" ] ];
    const holdings = [];
    let owners = [];
    if ( type === "works" ) {
        owners = ownerType === "Authors" ? resource.authors : resource.publishers;
    } else if ( type === "recordings" ) {
        owners = ownerType === "Artists" ? resource.additionalArtists : resource.labels;
    }

    owners.forEach( ( owner ) => {
        const percentage = parseFloat( resource.splits.get( owner ) );
        holdings.push( { name: owner.trim(), percentage } );
    } );
    holdings.sort( ( a, b ) => b.percentage - a.percentage );
    holdings.forEach( ( { name, percentage } ) => data.push( [ name, percentage ] ) );
    return data;
};

const generateBodyForType = ( type, data ) => data.map( ( resource, index ) => {
    try {
        if ( type === "works" ) {
            const {
                resourceId, title, iswc, authors, roles, publishers,
            } = resource;
            return (
                <Table.Row key={ resource.title + index }>
                    <Table.Cell verticalAlign="top">{resourceId}</Table.Cell>
                    <Table.Cell verticalAlign="top">{title}</Table.Cell>
                    <Table.Cell verticalAlign="top">{iswc}</Table.Cell>
                    <Table.Cell verticalAlign="top">
                        {authors.length > 0 && (
                            <Chart
                                chartType="BarChart"
                                loader={ <Loader active inline="centered" /> }
                                data={ calculateSplits( resource, type, "Authors" ) }

                                options={ {
                                    bar: {
                                        groupWidth: "75%",
                                    },
                                    legend: { position: "none" },
                                    hAxis: {
                                        minValue: 0,
                                        maxValue: 100,
                                    },
                                    chartArea: {
                                        height: "100%",
                                        width: "100%",
                                        top: 0,
                                        left: 140,
                                        right: 48,
                                        bottom: 48,
                                    },
                                    height: `${ ( 1 + authors.length ) * 20 + 60 }px`,
                                    width: "300px",
                                    isStacked: false,
                                    animation: {
                                        duration: 500,
                                        easing: "out",
                                        startup: true,
                                    },
                                    colors: [ "#4B13A3", "#42d63a" ],
                                } }
                            />
                        )
                        }
                    </Table.Cell>
                    <Table.Cell verticalAlign="top">
                        {authors.map( author => <p style={ { marginBottom: "24px" } }>{roles.get( author ).map( r => r.trim() )}</p> )}

                    </Table.Cell>

                    <Table.Cell>
                        {publishers.length > 0
&& (
    <Chart
        chartType="BarChart"
        loader={ <Loader active inline="centered" /> }
        data={ calculateSplits( resource, type, "Publishers" ) }

        options={ {
            bar: {
                groupWidth: "75%",
            },
            legend: { position: "none" },
            hAxis: {
                minValue: 0,
                maxValue: 100,
            },
            chartArea: {
                height: "100%",
                width: "100%",
                top: 0,
                left: 140,
                right: 48,
                bottom: 48,
            },
            height: `${ ( 1 + publishers.length ) * 20 + 60 }px`,
            width: "300px",
            isStacked: false,
            animation: {
                duration: 500,
                easing: "out",
                startup: true,
            },
            colors: [ "#4B13A3", "#42d63a" ],
        } }
    />
)}
                    </Table.Cell>

                    <Table.Cell>

                    </Table.Cell>

                </Table.Row>
            );
        }
        if ( type === "recordings" ) {
            const {
                resourceId, title, versionTitle, duration, isrc, creditedArtist, additionalArtists, roles, labels,
            } = resource;
            // "Resource ID", "Title", "Credited Artist", "Version Title", "ISRC", "Duration", "Artists", "Roles", "Labels"
            return (
                <Table.Row key={ resource.title + index }>
                    <Table.Cell verticalAlign="top">{resourceId}</Table.Cell>
                    <Table.Cell verticalAlign="top">{title}</Table.Cell>
                    <Table.Cell verticalAlign="top">{creditedArtist}</Table.Cell>
                    <Table.Cell verticalAlign="top">{versionTitle}</Table.Cell>
                    <Table.Cell verticalAlign="top">{isrc}</Table.Cell>
                    <Table.Cell verticalAlign="top">{duration}</Table.Cell>
                    <Table.Cell verticalAlign="top">
                        {additionalArtists.length > 0 && (
                            <Chart
                                chartType="BarChart"
                                loader={ <Loader active inline="centered" /> }
                                data={ calculateSplits( resource, type, "Artists" ) }

                                options={ {
                                    bar: {
                                        groupWidth: "75%",
                                    },
                                    legend: { position: "none" },
                                    hAxis: {
                                        minValue: 0,
                                        maxValue: 100,
                                    },
                                    chartArea: {
                                        height: "100%",
                                        width: "100%",
                                        top: 0,
                                        left: 140,
                                        right: 48,
                                        bottom: 48,
                                    },
                                    height: `${ ( 1 + additionalArtists.length ) * 20 + 60 }px`,
                                    width: "300px",
                                    isStacked: false,
                                    animation: {
                                        duration: 500,
                                        easing: "out",
                                        startup: true,
                                    },
                                    colors: [ "#4B13A3", "#42d63a" ],
                                } }
                            />
                        )
                        }
                    </Table.Cell>
                    <Table.Cell verticalAlign="top">
                        {additionalArtists.map( artist => <p style={ { marginBottom: "24px" } }>{roles.get( artist ).trim() }</p> )}

                    </Table.Cell>

                    <Table.Cell>
                        {labels.length > 0
&& (
    <Chart
        chartType="BarChart"
        loader={ <Loader active inline="centered" /> }
        data={ calculateSplits( resource, type, "Labels" ) }

        options={ {
            bar: {
                groupWidth: "75%",
            },
            legend: { position: "none" },
            hAxis: {
                minValue: 0,
                maxValue: 100,
            },
            chartArea: {
                height: "100%",
                width: "100%",
                top: 0,
                left: 140,
                right: 48,
                bottom: 48,
            },
            height: `${ ( 1 + labels.length ) * 20 + 60 }px`,
            width: "300px",
            isStacked: false,
            animation: {
                duration: 500,
                easing: "out",
                startup: true,
            },
            colors: [ "#4B13A3", "#42d63a" ],
        } }
    />
)}
                    </Table.Cell>

                    <Table.Cell>

                    </Table.Cell>

                </Table.Row>
            );
        }
    } catch ( error ) {
        console.log( error );
        return (
            <Table.Row key={ resource.title + index }>
                <Table.Cell>invalid</Table.Cell>
            </Table.Row>
        );
    }
} );

const StyledOutput = ( {
    type, onChangeView, data,
} ) => (
    <Table basic>
        {generateHeaderForType( type, onChangeView )}
        <Table.Body>
            {generateBodyForType( type, data )}
        </Table.Body>

        <Table.Footer></Table.Footer>
    </Table>

);
StyledOutput.propTypes = {
    type: string,
    onChangeView: func.isRequired,
    data: arrayOf( shape( {} ) ).isRequired,
};

StyledOutput.defaultProps = {
    type: "works",
};
export default StyledOutput;
