import { all } from "redux-saga/effects";

// Imported sagas
import { sagas as app } from "./app/duck";
import { sagas as catalogue } from "./pages/catalogue/duck";
import { sagas as profile } from "./pages/profile/duck";
import { sagas as registerSongs } from "./pages/register-songs/duck";
import { sagas as verifications } from "./pages/verifications/duck";
import { sagas as distribution } from "./pages/distribution/duck";
import { sagas as error } from "./components/error/duck";

const sagas = [
    // Combined sagas
    app(),
    catalogue(),
    profile(),
    registerSongs(),
    verifications(),
    distribution(),
    error(),
];

export default function* root() { yield all( sagas ); }
