/* eslint-disable complexity */
import React, { Component } from "react";
import { bool, func, shape } from "prop-types";
import {
    Segment, SegmentGroup, Form, Button, Header,
} from "semantic-ui-react";

import countryList from "iso-3166-country-list";

const countries = countryList.map( country => ( { key: country.code, text: country.name, value: country.code } ) );

class PublisherTab extends Component {
    constructor( props ) {
        super( props );

        this.state = {
            country: null,
            isni: null,
            ipi: null,
            cae: null,
            contact: null,
            name: null,
            type: null,
        };
    }

    save=() => {
        const {
            country, isni, ipi, cae, contact, name, type,
        } = this.state;
        const { onSave, publisher } = this.props;
        onSave( {
            country: country || publisher.country,
            isni: isni || publisher.isni,
            ipi: ipi || publisher.ipi,
            cae: cae || publisher.cae,
            name: name || publisher.name,
            contact: contact || publisher.contact,
            type: type || publisher.type,
        } );
    }

    render() {
        const { saving, publisher } = this.props;
        const {
            isni, ipi, cae, contact, name,
        } = this.state;
        return (
            <div className="page-content">
                <SegmentGroup>
                    <Segment>
                        <Header as="h4">Publisher</Header>

                    </Segment>
                    <Segment className="form-body">
                        <Form>
                            <Form.Group widths="equal">
                                <Form.Input
                                    label="Name"
                                    required
                                    value={ publisher && !name ? publisher.name : name }
                                    placeholder="Name"
                                    onChange={ ( e ) => {
                                        this.setState( { name: e.currentTarget.value } );
                                    } }
                                />
                                <Form.Input
                                    label="Contact"
                                    required
                                    value={ publisher && !contact ? publisher.contact : contact }
                                    placeholder="E-mail or cellphone number"
                                    onChange={ ( e ) => {
                                        this.setState( { contact: e.currentTarget.value } );
                                    } }
                                />
                                <Form.Select
                                    required
                                    label="Country"
                                    defaultValue={ publisher ? publisher.country : null }
                                    placeholder="Select a country"
                                    selection
                                    options={ countries }
                                    onChange={ ( e, { value } ) => {
                                        this.setState( { country: value } );
                                    } }
                                />
                                <Form.Select
                                    required
                                    label="Type"
                                    defaultValue={ publisher ? publisher.type : null }
                                    placeholder="Select a type"
                                    selection
                                    options={ [
                                        { key: "PUBLISHER", text: "Publisher", value: "PUBLISHER" },
                                        { key: "DISTRIBUTOR", text: "Distributor", value: "DISTRIBUTOR" },
                                    ] }
                                    onChange={ ( e, { value } ) => {
                                        this.setState( { country: value } );
                                    } }
                                />
                            </Form.Group>
                            <Form.Group widths="equal">

                                <Form.Input
                                    label="ISNI"
                                    value={ publisher && !isni ? publisher.isni : isni }
                                    placeholder="ISNI"
                                    onChange={ ( e ) => {
                                        this.setState( { isni: e.currentTarget.value } );
                                    } }
                                />
                                <Form.Input
                                    label="IPI"
                                    value={ publisher && !ipi ? publisher.ipi : ipi }
                                    placeholder="IPI"
                                    onChange={ ( e ) => {
                                        this.setState( { ipi: e.currentTarget.value } );
                                    } }
                                />
                                <Form.Input
                                    label="CAE"
                                    value={ publisher && !cae ? publisher.cae : cae }
                                    placeholder="CAE"
                                    onChange={ ( e ) => {
                                        this.setState( { cae: e.currentTarget.value } );
                                    } }
                                />
                            </Form.Group>

                        </Form>
                    </Segment>
                </SegmentGroup>

                <Button

                    className="primary"
                    loading={ saving }
                    onClick={ this.save }
                >
                    {publisher ? "Save" : "Confirm"}

                </Button>
            </div>
        );
    }
}
PublisherTab.propTypes = {
    onSave: func.isRequired,
    saving: bool.isRequired,
    publisher: shape( {} ),
};
PublisherTab.defaultProps = {
    publisher: null,
};
export default PublisherTab;
